<template>
  <div class="NetWorkMessage network">
    <div class="item_row">
      <card-module title="网关基本信息" :height="160">
        <template v-slot:module_con>
          <el-form :model="basicFormData" label-width="auto" size="small">
            <el-form-item label="网关名:">
              <el-input v-model="basicFormData.gatewayName" placeholder="" disabled/>
            </el-form-item>
            <el-form-item label="网关编码:">
              <el-input v-model="basicFormData.gatewayCode" placeholder="" disabled/>
            </el-form-item>
          </el-form>
        </template>
      </card-module>
      <card-module title="通信情况" :height="310">
        <template v-slot:operation>
           <el-button size="mini" type="primary">查询</el-button>
        </template>
        <template v-slot:module_con>
          <div class="c-blue fw-b m-b20">上次通信时间<span class="m-l10">2022-05-20 13:14:00</span></div>
          <el-form :model="basicFormData" label-width="auto" size="small">
            <el-form-item :label="item.label" v-for="item in communicateOption" :key="item.label">
              <el-input v-model="communicateFormData[item.key]" placeholder="" />
            </el-form-item>
          </el-form>
        </template>
      </card-module>
      <card-module title="NTP服务" :height="370">
        <template v-slot:module_con>
          <div class="m-b20 NTP">
            <el-radio-group v-model="NTPFormData['startUse']" class="ml-4">
              <el-radio label="0" size="large">启用</el-radio>
              <el-radio label="1" size="large">不启用</el-radio>
            </el-radio-group>
          </div>
          <div class="m-b30 NTP">
            <el-radio-group v-model="NTPFormData['startUse']" class="ml-4">
              <el-radio label="0" size="large">自定义服务器</el-radio>
              <el-radio label="1" size="large">自动选择</el-radio>
            </el-radio-group>
          </div>
         <el-form :model="basicFormData" label-width="auto" size="small">
            <el-form-item label="服务器地址:">
              <el-input v-model="basicFormData.user" placeholder="" />
            </el-form-item>
            <el-form-item label="端口:">
              <el-input v-model="basicFormData.user" placeholder="" />
            </el-form-item>
            <el-form-item label="更新周期:">
              <el-input v-model="basicFormData.user" placeholder="" />
            </el-form-item>
          </el-form>
          <div class="ta-c m-t35">
            <el-button size="mini" type="primary" class="m-r30">保存修改</el-button>
            <el-button size="mini">取消</el-button>
          </div>
        </template>
      </card-module>
    </div>
    <div class="item_row">
      <card-module title="IP配置" :height="160" :conStyle="{  padding: '0 10px' }">
        <template v-slot:operation>
           <el-button size="mini" type="primary">查询</el-button>
        </template>
        <template v-slot:module_con>
          <div class="two_sides net0">
            <div class="left_sides">NET0</div>
            <div class="right_sides">
              <div class="m-b20 IPConfig">
                <el-radio-group v-model="IPFormData0['startUse']" class="ml-4">
                  <el-radio label="0" size="large">静态 IP</el-radio>
                  <el-radio label="1" size="large">DHCP</el-radio>
                </el-radio-group>
              </div>
              <el-form :model="IPFormData0" label-width="auto" size="small">
                <el-form-item :label="item.label" v-for="item in IPOption" :key="item.label">
                  <el-input v-model="IPFormData0[item.key]" placeholder="" />
                </el-form-item>
              </el-form>
            </div>
          </div>
          <div class="two_sides net1">
            <div class="left_sides">NET1</div>
            <div class="right_sides">
              <div class="m-b20 IPConfig">
                <el-radio-group v-model="IPFormData1['startUse']" class="ml-4">
                  <el-radio label="0" size="large">静态 IP</el-radio>
                  <el-radio label="1" size="large">DHCP</el-radio>
                </el-radio-group>
              </div>
              <el-form :model="IPFormData1" label-width="auto" size="small">
                <el-form-item :label="item.label" v-for="item in IPOption" :key="item.label">
                  <el-input v-model="IPFormData1[item.key]" placeholder="" />
                </el-form-item>
              </el-form>
            </div>
          </div>
          <div class="ta-c m-t35 p-b30">
            <el-button size="mini" type="primary" class="m-r30">确认</el-button>
            <el-button size="mini">取消</el-button>
          </div>
        </template>
      </card-module>
    </div>
    <div class="item_row">
      <card-module title="4G设置" :height="370">
        <template v-slot:module_con>
          <div class="m-b20 NTP">
            <el-radio-group v-model="fourGFormData['startUse']" class="ml-4">
              <el-radio label="0" size="large">启用</el-radio>
              <el-radio label="1" size="large">不启用</el-radio>
            </el-radio-group>
          </div>
          <el-form :model="fourGFormData" label-width="auto" size="small">
            <el-form-item :label="item.label" v-for="item in fourGOption" :key="item.label">
              <el-input v-model="fourGFormData[item.key]" placeholder="" />
            </el-form-item>
          </el-form>
          <div class="ta-c m-t35">
            <el-button size="mini" type="primary" class="m-r30">保存修改</el-button>
            <el-button size="mini">取消</el-button>
          </div>
        </template>
      </card-module>
      <card-module title="网络调试" :height="260">
        <template v-slot:operation>
           <el-button size="mini" type="primary">运行</el-button>
        </template>
        <template v-slot:module_con>
          <div class="ping_wra m-b20">
            <span class="m-r5">ping:</span>
            <el-dropdown @command="handleCommand" class="m-r5">
              <span class="el-dropdown-link">
                {{ Eth }}:
              </span>
              <template #dropdown>
                <el-dropdown-menu>
                  <el-dropdown-item command="Eth0">Eth0</el-dropdown-item>
                  <el-dropdown-item command="Eth1">Eth1</el-dropdown-item>
                </el-dropdown-menu>
              </template>
            </el-dropdown>
            <el-input size="small" v-model="debugFormData['eth']"></el-input>
          </div>
          <el-input
            type="textarea"
            :autosize="{ minRows: 4, maxRows: 6 }"
            v-model="debugFormData['content']"
          >
          </el-input>
        </template>
      </card-module>
    </div>
    <div class="item_row">
      <card-module title="WIFI配置" :height="370">
        <template v-slot:operation>
           <el-button size="mini" type="primary">编辑</el-button>
        </template>
       <template v-slot:module_con>
          <div class="m-b20 NTP">
            <el-radio-group v-model="WiFiFormData['startUse']" class="ml-4">
              <el-radio label="0" size="large">启用</el-radio>
              <el-radio label="1" size="large">不启用</el-radio>
            </el-radio-group>
          </div>
           <div class="m-b20 NTP">
            <el-radio-group v-model="WiFiFormData['startUse']" class="ml-4">
              <el-radio label="0" size="large">Station</el-radio>
              <el-radio label="1" size="large">AP</el-radio>
            </el-radio-group>
          </div>
          <el-form :model="WiFiFormData" label-width="auto" size="small">
            <el-form-item :label="item.label" v-for="item in WiFiOption" :key="item.label">
              <el-input v-model="WiFiFormData[item.key]" placeholder="" />
            </el-form-item>
          </el-form>
          <div class="ta-c m-t35">
            <el-button size="mini" type="primary" class="m-r30">保存修改</el-button>
            <el-button size="mini">取消</el-button>
          </div>
        </template>
      </card-module>
    </div>
  </div>
</template>

<script>
import CardModule from '../CardModule';
import { listGateways } from '@/api/publishConfig';
export default {
  name: 'NetWorkMessage',
  components: {
    CardModule
  },
  props: {
    param: {
      type: Object,
      default: () => {
        return {
          gatewayId: null,
          gatewayCode: null
        };
      }
    }
  },
  data() {
    return {
      Eth: 'Eth0',
      basicFormData: {
        gatewayName: '',
        gatewayCode: ''
      },
      communicateFormData: {
        use: ''
      },
      NTPFormData: {
        startUse: '0'
      },
      IPFormData0: {
        startUse: '0'
      },
      IPFormData1: {
        startUse: '0'
      },
      fourGFormData: {
        startUse: '0'
      },
      WiFiFormData: {
        startUse: '0'
      },
      debugFormData: {
        eth: ''
      },
      communicateOption: [{
        key: 'use',
        label: 'CPU占用:'
      }, {
        key: 'use',
        label: 'MEM占用:'
      }, {
        key: 'use',
        label: '磁盘占用:'
      }, {
        key: 'use',
        label: '程序版本:'
      }],
      IPOption: [{
        key: 'ip',
        label: 'IP:'
      }, {
        key: 'use',
        label: 'gateway:'
      }, {
        key: 'use',
        label: 'netmask:'
      }],
      fourGOption: [{
        key: 'use',
        label: 'APN:'
      }, {
        key: 'use',
        label: '用户名:'
      }, {
        key: 'use',
        label: '密码:'
      }, {
        key: 'use',
        label: 'PIN码:'
      }],
      WiFiOption: [{
        key: 'ip',
        label: 'SSID:'
      }, {
        key: 'use',
        label: 'Password:'
      }]
    };
  },
  created() {
    this.loadData();
  },
  methods: {
    // 刷新
    reLoad() {
      this.loadData();
    },
    // 加载数据
    loadData() {
      // 网关基本信息
      listGateways({
        pageNum: 1,
        pageSize: 1,
        id: this.param.gatewayId
      }).then(res => {
        const { gatewayCode, gatewayName } = res.data[0];
        this.basicFormData = {
          gatewayCode,
          gatewayName
        };
      });
    },
    handleCommand(val) {
      this.Eth = val;
    }
  }
};
</script>

<style lang="less" scoped>
.NetWorkMessage {
  display: flex;
  // height: 100%;
  // overflow-y: auto;
  padding: 10px;

  .item_row {
    width: 23%;
    height: 100%;
    margin-right: 10px;

    .two_sides {
      display: flex;
      align-items: center;

      .left_sides {
        padding-right: 30px;
        font-weight: bold;
        color: #0871DC;
      }
      .right_sides {
        flex: 1;
      }
    }
    .net0 {
      position: relative;
      padding-bottom: 20px;

      &::after {
        position: absolute;
        left: -20px;
        bottom: 0;
        content: '';
        width: calc(100% + 40px);
        height: 1px;
        border-bottom: 1px dashed #D5D5D5;
      }
    }
    .net1 {
      padding-top: 30px;
    }

    .ping_wra {
      display: flex;
      align-items: center;
      font-weight: bold;
      color: #040404;
    }
  }
}
</style>
