<template>
<el-dialog
  :class="[$store.state.dialogMarginClass,'import-dialog']"
  :title="'网关内参数'"
  :width="'80%'"
  :visible.sync="isShow">
    <div class="box" >
      <p class="time" >响应时间：{{nowTime}}</p>
      <VXETable
          id="table"
          :resizable="true"
          :autoResize="true"
          :tableName="'user'"
          :height="'500px'"
          :tableLoading="tableLoading"
          :pager-config="tablePage"
          :tableDatas="tableDatas"
          :tableColumns="tableColumns"
          :tablePage="tablePage"
          :highlightCurrentRow="true"
          @onTablePageChange="onTablePageChange"
          ref="xGrid"
        >
        </VXETable>
    </div>
    <span slot="footer" class="dialog-footer">
        <el-button size="mini" @click="hidePublish()"  >{{BTN_TEXT.CLOSE}}</el-button>
    </span>
  </el-dialog>
</template>

<script>
import VXETable from '@/components/common/Table';
import { TITLE, BTN_TEXT, VARIABLE } from '@/utils/constantsFile';
import { showWarningMessage } from '@/utils/message';
import { showErrorNotify } from '@/utils/notify';
import {
  bizQueryEdgePointData
} from '@/api/publishConfig';
export default {
  components: {
    VXETable
  },
  props: {
    importDialogName: {
      type: String,
      default: ''
    },
    exportFileName: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      BTN_TEXT: BTN_TEXT,
      VARIABLE: VARIABLE,
      importFileName: '',
      importFiles: [],
      // 上传进度条进度值
      uploadPercent: 0,
      // 进度条是否显示
      isUploadProgressShow: false,
      uploadProgressColor: '#0871dc',
      errorList: [],
      isShow: false,
      tableLoading: false,
      // 模态框输入框的宽度
      formLabelWidth: '100px',
      // 表格字段
      tableDatas: [],
      // 表头
      tableColumns: [],
      tablePage: {
        total: 0,
        currentPage: 1,
        pageSize: 10,
        pageSizes: [5, 10, 20, 50, 100, 200, 500],
        layouts: ['Sizes', 'PrevJump', 'PrevPage', 'Number', 'NextPage', 'NextJump', 'FullJump', 'Total'],
        perfect: true,
        slots: {
          // 使用自定义插槽
          left: 'pager_left'
        }
      },
      nowAcquireData: {},
      gatewayCode: '',
      nowTime: ''
    };
  },
  computed: {},
  created: function() {
  },
  methods: {
    initData() {
      this.nowAcquireData = {};
      this.gatewayCode = '';
      this.nowTime = '';
      this.tableDatas = [];
      this.tableColumns = [];
      this.tablePage.currentPage = 1;
      this.tablePage.pageSize = 10;
      this.tablePage.total = 0;
    },
    /**
     * @description: 分页
     * @param currentPage 第几页
     * @param pageSize 一页几条
     * @return: 无
     * @author: ZJM
     */
    handleOptionTablePageChange(currentPage, pageSize) {
      this.tablePage.currentPage = currentPage;
      this.tablePage.pageSize = pageSize;
      this.findTableList();
    },
    /**
     * @description: 获取表格数据
     * @param {null} 无
     * @return: void
     */
    async findTableList() {
      try {
        // 播放表格加载动画
        this.tableLoading = true;
        const params = {
          gatewayCode: this.gatewayCode,
          pageNum: this.tablePage.currentPage,
          pageSize: this.tablePage.pageSize,
          acquireConfigId: this.nowAcquireData.acquireConfigId
        };

        const res = await bizQueryEdgePointData(params);
        if (res.code === 0) {
          this.tableDatas = res.data;
          this.tablePage.total = res.size;
          if (this.nowTime === '') {
            this.nowTime = this.$options.filters.formatTime(new Date().getTime());
          }
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
        this.tableLoading = false;
      }
    },
    createColumns(data, code) {
      this.gatewayCode = code;
      this.nowAcquireData = data;
      console.log(this.nowAcquireData);
      this.tableColumns = [];
      this.tableColumns.push(
        {
          type: 'seq',
          title: '序号',
          minWidth: '10',
          align: 'center'
          // sortable: true
        }
      );
      var arr = this.nowAcquireData.pointDefinition;
      for (var i = 0; i < arr.length; i++) {
        this.tableColumns.push({
          field: arr[i].name,
          title: arr[i].explaination,
          align: 'center'
        });
      }
      this.findTableList();
    },
    hidePublish() {
      this.isShow = false;
      this.$emit('closeImportDialog');
    }
  }
};
</script>

<style lang="less"  scoped>
.box{
  padding: 10px!important;
}
.time{
  text-align: left;
  height: 30px;
  margin: 0;
  line-height: normal;
}
</style>
