<template>
<div class="network_detail network">
  <div class="header_con">
    <div  v-for="(item, index) in operList" :key="index">
      <div
        class="item_oper"
        v-if="item.hasPower == 'ALL' || item.hasPower.includes(componentName)"
        @click="handleOper(item)"
      >
        <i :class="['icon', item.iconName]"></i>
        <span class="name">{{ item.name }}</span>
      </div>
    </div>
  </div>
  <div class="main_con">
    <el-radio-group v-model="componentName">
      <el-radio-button v-for="item in radioList" :key="item.name" :label="item.component">{{ item.name }}</el-radio-button>
    </el-radio-group>
    <el-button class="back_btn" size="small" type="primary" @click="handleBack">返回</el-button>
    <div class="tabs_con">
      <component :is="componentName" ref="childRef" :param="param"></component>
    </div>
  </div>
</div>
</template>

<script>
import NetWorkMessage from './components/NetWorkMessage';
import ConcatConfigMessage from './components/ConcatConfigMessage';
import ParmConfig from './components/ParmConfig';
import PublishConfig from './components/PublishConfig';
import MaskList from './components/MaskList';
export default {
  name: 'NetWorkDetail',
  components: {
    NetWorkMessage,
    ConcatConfigMessage,
    ParmConfig,
    PublishConfig,
    MaskList
  },
  props: {
    param: {
      type: Object,
      default: () => {
        return {
          gatewayId: null,
          gatewayCode: null
        };
      }
    }
  },
  data() {
    return {
      componentName: 'NetWorkMessage',
      radioList: [{
        name: '网关信息',
        component: 'NetWorkMessage'
      }, {
        name: '连接配置',
        component: 'ConcatConfigMessage'
      }, {
        name: '参数配置',
        component: 'ParmConfig'
      }, {
        name: '发布配置',
        component: 'PublishConfig'
      }, {
        name: '任务列表',
        component: 'MaskList'
      }],
      operList: [{
        name: '刷新',
        iconName: 'icon-refresh',
        hasPower: 'ALL'
      }, {
        name: '新增连接',
        iconName: 'icon-add',
        hasPower: ['ConcatConfigMessage']
      }, {
        name: '新增参数',
        iconName: 'icon-add',
        hasPower: ['ParmConfig']
      }, {
        name: '批量导入',
        iconName: 'icon-import',
        hasPower: ['ParmConfig']
      }, {
        name: '新增发布',
        iconName: 'icon-import',
        hasPower: ['PublishConfig']
      }, {
        name: '新增任务',
        iconName: 'icon-import',
        hasPower: ['MaskList']
      }]
    };
  },
  methods: {
    handleOper({ name }) {
      switch (name) {
        case '刷新':
          console.log('刷新页面');
          this.$refs.childRef.reLoad();
          break;
        case '新增连接':
          console.log('新增连接');
          this.$refs.childRef.handleAddConcat();
          break;
        case '新增参数':
          this.$refs.childRef.showAddParamDialog();
          break;
        case '批量导入':
          this.$refs.childRef.shoImportDialog();
          break;
        case '新增发布':
          this.$refs.childRef.addPublish();
          break;
        case '新增任务':
          this.$refs.childRef.handleAddMask();
          break;
      }
    },
    handleBack() {
      this.$emit('handleBack');
    }
  }
};

</script>

<style lang="less" scoped>
@import './styles/common.less';
.main_con {
  position: relative;
  height: calc(100% - 40px);
  background-color: #F2F2F2;
  padding: 10px 10px 0 10px;

  .back_btn {
    position: absolute;
    top: 10px;
    right: 15px;
  }
  .tabs_con {
    height: calc(100% - 40px);
    overflow-y: auto;
    background-color: #ffffff;
  }

}
</style>
