
// 导入title
import HeaderTitle from '@/components/common/PageNavBar/HeaderTitle';
import HeadOperationItem from '@/components/common/PageNavBar/HeadOperationItem';
import VXETable from '@/components/common/Table';
import NetWorkDetail from '@/views/main/NetWorkDetail';
import {
  listGateways,
  insertGateway,
  updateGatewayPwd,
  getList
} from '@/api/publishConfig.js';
export default {
  data() {
    return {
      tableLoading: false,
      dialogVisible: false,
      detailParam: {},
      selectRow: null,
      showEdit: false,
      islist: true,
      // 搜索框
      formData: {
        gatewayType: '',
        companyName: ''
      },
      // 下划公司列表
      companyList: [],
      // 新增模态框
      NameList: [
        { label: '公司一', value: '0' },
        { label: '公司二', value: '1' }
      ],
      formTable: {
        companyCode: '',
        gatewayCode: '',
        gatewayName: '',
        gatewayType: '',
        companyName: ''

      },
      // 密码模态框数据
      form2: {
        NewPassWord: '',
        RePassWord: ''
      },
      // 模态框校验规则
      // rolerules1: {
      //   moduleName: [
      //     { required: true, message: '请输入模块名称', trigger: 'blur' }
      //   ],
      //   description: [
      //     { required: true, message: '请输入说明信息', trigger: 'blur' }
      //   ]
      // },
      // 模态框输入框的宽度
      formLabelWidth: '100px',
      // 表格字段
      tableData: [
        { gatewayName: '11' }
      ],
      // 分页
      tablePage: {
        total: 0,
        currentPage: 1,
        pageSize: 10,
        pageSizes: [5, 10, 20, 50, 100, 200, 500],
        layouts: ['PrevJump', 'PrevPage', 'Number', 'NextPage', 'NextJump', 'Sizes', 'Total'],
        perfect: true,
        slots: {
          // 使用自定义插槽
          left: 'pager_left'
        }
      }
    };
  },
  components: {
    HeaderTitle,
    HeadOperationItem,
    VXETable,
    NetWorkDetail
  },
  filters: {
    // 样式过滤
    statusStyle(data) {
      switch (data) {
        case true :return 'primary';
        case false : return 'info';
        default: return data;
      }
    },

    // 内容过滤
    statusContent(data) {
      switch (data) {
        case true : return '在线';
        case false : return '离线';
        default: return data;
      }
    },

    // 时间过滤
    timeSwitch(value) {
      const date = new Date(value);
      const y = date.getFullYear();
      let MM = date.getMonth() + 1;
      MM = MM < 10 ? ('0' + MM) : MM;
      let d = date.getDate();
      d = d < 10 ? ('0' + d) : d;
      let h = date.getHours();
      h = h < 10 ? ('0' + h) : h;
      let m = date.getMinutes();
      m = m < 10 ? ('0' + m) : m;
      let s = date.getSeconds();
      s = s < 10 ? ('0' + s) : s;
      return y + '-' + MM + '-' + d + ' ' + h + ':' + m + ':' + s;
    }
  },
  methods: {
    // 访问
    handelVisit({ id, gatewayCode }) {
      this.detailParam = {
        gatewayId: id,
        gatewayCode
      };
      this.islist = false;
    },
    // 返回
    handleBack() {
      this.islist = true;
    },
    // 刷新
    handleReload() {

    },
    handlePageChange1({ currentPage, pageSize }) {
      this.tablePage.currentPage = currentPage;
      this.tablePage.pageSize = pageSize;
      this.listData();
    },
    // 表格加载数据
    listData() {
      this.tableLoading = true;
      const conditionJson = {
        pageNum: this.tablePage.currentPage,
        pageSize: this.tablePage.pageSize
      };
      listGateways(conditionJson).then(res => {
        this.tableLoading = false;
        // this.tableData = res.data;
        this.tablePage.total = res.data.length;
        this.tableData = res.data.slice((this.tablePage.currentPage - 1) * this.tablePage.pageSize, this.tablePage.currentPage * this.tablePage.pageSize);
      });
    },
    // 切换页码
    // onTablePageChange(currentPage, pageSize) {
    //   this.tablePage.currentPage = currentPage;
    //   this.tablePage.pageSize = pageSize;
    //   this.listData();
    // },
    // 关闭新增模态框
    closeit() {
      this.$refs.roleForm.resetFields();
    },
    // 修改密码模态框显示
    modifyPassword(row) {
      this.dialogVisible = true;
      this.selectRow = row;
    },
    // 点击新增按钮触发的方法
    insertEvent() {
      this.showEdit = true;
    },
    // 修改密码确认按钮
    PasswordConfirm(row) {
      const conditionJson = {
        id: this.selectRow.id,
        pwd: this.form2.NewPassWord
      };
      updateGatewayPwd(conditionJson).then(res => {
        if (res.code === 0) {
          this.dialogVisible = false;
          this.$message({
            message: '修改密码成功',
            type: 'success'
          });
        }
      });
    },
    // 新增确定按钮
    submitEvent() {
      this.tableLoading = true;
      const conditionJson = {
        companyCode: this.formTable.companyCode,
        gatewayName: this.formTable.gatewayCode,
        companyName: this.formTable.companyName,
        gatewayType: this.formTable.gatewayName,
        // 验证密码只能用一次，再次验证需重新获取
        gatewayCaptcha: 295865
      };
      insertGateway(conditionJson).then(res => {
        if (res.code === 0) {
          this.$message({
            message: '新增成功',
            type: 'success'
          });
          this.$refs.xTable.insert(this.formTable);
          this.tableLoading = false;
        }
      });
    },
    handlePageChange({ currentPage, pageSize }) {
      this.tablePage.currentPage = currentPage;
      this.tablePage.pageSize = pageSize;
      this.listData();
    },
    async findCompanyList() {
      try {
        this.tableLoading = true;
        const res = await getList(
          {
            size: this.tablePage.pageSize,
            current: this.tablePage.currentPage
          },
          {
            conditionJson1: [{
              column: 'state', // 参数名
              operator: 1, // 并且
              ptype: 'string', // 参数类型
              type: 'in', // 包含
              value: '1,3'// 参数值
            }]
          }
        );
        if (res.code === '0000') {
          // 将总条数写入table分页配置
          this.tablePage.total = res.total;
          // 加载动画消失
          this.tableLoading = false;
          this.companyList = res.rows;
          console.log(res.rows);
        }
      } catch (error) {
        // showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    }
  },
  created() {
    this.listData();
    this.findCompanyList();
  }
};
