<template>
  <!-- <h1>网关信息</h1> -->
  <div>
    <div class="leftTask" >
      <div class="toolBox" >
        <span>连接：</span>
        <el-select
          size="mini"
          v-model="acquierValue"
          style="width: 100px"
          :placeholder="VARIABLE.PLEASE_CHOOSE"
        >
          <el-option
            v-for="item in acquierOption"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          ></el-option>
        </el-select>
        <el-button type="primary" size="mini" @click="initAcquireSearch()" >{{BTN_TEXT.SEARCH}}</el-button>
        <!-- <el-button size="mini" @click="hidePublish()"  >{{BTN_TEXT.RESET}}</el-button> -->

        <el-button-group style="float: right;" >
          <el-button type="primary" size="mini" @click="showParamDialog()" >查看网关内参数</el-button>
          <el-button type="primary" size="mini" @click="toCloud()" >网关同步到平台</el-button>
          <el-button type="primary" size="mini" @click="toEdge()" >平台同步到网关</el-button>
        </el-button-group>

      </div>
      <VXETable
          id="table"
          :resizable="true"
          :autoResize="true"
          :tableName="'user'"
          :height="$options.filters.getTableHeight(250)"
          :tableLoading="tableLoading"
          :pager-config="tablePage"
          :tableDatas="tableDatas"
          :tableColumns="tableColumns"
          :tablePage="tablePage"
          :highlightCurrentRow="true"
          @onTablePageChange="onTablePageChange"
          ref="xGrid"
        >
        </VXETable>
    </div>
    <div class="rightTask" v-if="isShowInfo"  >
      <div class="rightTaskCon" >
       <el-form class="form2" ref="editCodeForm">
        <el-row :gutter="22">
          <el-col v-for="(item,index) in protocolDefinition_x" :key="index" :span="22">
            <el-form-item
            :label="item.explaination"
            :label-width="formLabelWidth"  >

              <el-input
               v-if="item.optionList.length===0"
               v-model="item.nowValue"
               :type=funcType(item.dataTypeEnum)
              ></el-input>

              <el-select
                v-if="item.optionList.length>0"
                v-model="item.nowValue"
                style="width: 100%"
                :placeholder="VARIABLE.PLEASE_CHOOSE"
              >
                <el-option
                  v-for="itemx in item.optionJson"
                  :key="itemx.value"
                  :label="itemx.label"
                  :value="itemx.value"
                ></el-option>
              </el-select>

            </el-form-item>
          </el-col>
          </el-row >
        </el-form>
        <el-button type="primary" size="mini" @click="toUpdate()" >{{BTN_TEXT.SAVE+'修改'}}</el-button>
       <el-button size="mini" @click="resetUpdate()" >{{BTN_TEXT.CANCEL}}</el-button>
      </div>
    </div>

    <el-dialog
      :class="[$store.state.dialogMarginClass]"
      :title="'新增参数'"
      :width="'550px'"
      :visible.sync="dialogVisibleAdd"
    >
      <div class="rightTaskCon2" >
       <el-form class="form2" ref="editCodeForm">
        <el-row :gutter="22">
          <el-col v-for="(item,index) in protocolDefinition" :key="index" :span="22">
            <el-form-item
            :label="item.explaination"
            :label-width="formLabelWidth"  >

              <el-input
               v-if="item.optionList.length===0"
               v-model="item.nowValue"
               :type=funcType(item.dataTypeEnum)
              ></el-input>

              <el-select
                v-if="item.optionList.length>0"
                v-model="item.nowValue"
                style="width: 100%"
                :placeholder="VARIABLE.PLEASE_CHOOSE"
              >
                <el-option
                  v-for="itemx in item.optionJson"
                  :key="itemx.value"
                  :label="itemx.label"
                  :value="itemx.value"
                ></el-option>
              </el-select>

            </el-form-item>
          </el-col>
          </el-row >
        </el-form>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" size="mini" @click="saveAdd()" >{{BTN_TEXT.SAVE}}</el-button>
        <el-button size="mini" @click="hideParamDialog()"  >{{BTN_TEXT.CANCEL}}</el-button>
      </span>
    </el-dialog>

    <paramDialog
       ref="paramDialog"
       v-if="isParamDialogShow"
       :acquireData="nowAcquireData"
       @closeImportDialog="closeImportDialog"
    ></paramDialog>

    <ImportDialog
        ref="ImportDialog"
        v-if="isImportDialogShow"
        :importDialogName="'批量导入'"
        :exportFileName="'模板.xlsx'"
        @exportTemplate="exportTemplate"
        @submitImportFile="submitImportFile"
        @closeImportDialog="closeImportDialog"
    ></ImportDialog>

  </div>
</template>

<script>
import VXETable from '@/components/common/Table';
import {
  TITLE,
  VARIABLE,
  BTN_TEXT,
  EXPORT_NAME
} from '@/utils/constantsFile';
import {
  acquireListAllAcquireConfigsInShort,
  acquireListAcquirePointData,
  acquireUploadPointDataExcel,
  acquirnInsertOneAcquirePointData,
  acquireUpdateAcquirePointData,
  acquireDeleteAcquirePointData,
  acquirePointDataOverwriteToCloud,
  acquirePointDataOverwriteToEdge
} from '@/api/publishConfig';
import { showWarningMessage, showSuccessMessage } from '@/utils/message';
import { showErrorNotify } from '@/utils/notify';
import ImportDialog from '@/components/common/ImportDialogNew';
// 引入组件
import paramDialog from '@/views/main/NetWorkDetail/components/ParmConfig/param.vue';
export default {
  name: 'RoleManagement',
  props: {
    param: {
      type: Object,
      default: () => {
        return {
          gatewayId: null,
          gatewayCode: null
        };
      }
    }
  },
  data() {
    return {
      EXPORT_NAME: EXPORT_NAME,
      BTN_TEXT: BTN_TEXT,
      VARIABLE: VARIABLE,
      tabPosition: 'wTop',
      publishData: {},
      // gatewayCode: '123',
      // publishConfigId: 2,
      tableLoading: false,
      // 模态框输入框的宽度
      formLabelWidth: '120px',
      // 表格字段
      tableDatas: [],
      // 表头
      tableColumns: [
        {
          type: 'seq',
          title: '序号',
          minWidth: '10',
          align: 'center'
          // sortable: true
        },
        {
          field: 'pointName',
          title: '参数名',
          align: 'center'
          // sortable: true
        },
        {
          field: 'pointAbbrName',
          title: '别名',
          align: 'center'
          // formatter: this.formatUser
          // sortable: true
        },
        {
          field: 'configName',
          title: '连接名称',
          align: 'center'
        },
        {
          field: 'pointStatus',
          title: '生效状态',
          minWidth: '80',
          align: 'center',
          slots: {
            default: ({ row, column }) => {
              if (row.pointStatus === 'NOT_IN_USE') {
                return [<span class="noPass" >未同步</span>];
              } else {
                return [<span class="pass" >已生效</span>];
              }
            }
          }
          // sortable: true
        },
        {
          field: 'linkTime',
          title: '生效时间',
          align: 'center',
          slots: {
            default: ({ row, column }) => {
              if (row.linkTime) {
                return this.$options.filters.formatTime(row.linkTime);
              } else {
                return '';
              }
            }
          }
          // sortable: true
        },
        {
          title: '操作',
          width: '300',
          align: 'center',
          fixed: 'right',
          slots: {
            default: ({ row, column }) => {
              return [
                <vxe-button type="text" status="primary" content="详情" onClick={() => this.toInfo(row, column)} ></vxe-button>,
                <vxe-button type="text" status="danger" content="删除" onClick={() => this.toDeleteAcquire(row, column)} ></vxe-button>
              ];
            }
          }
        }
      ],
      tablePage: {
        total: 0,
        currentPage: 1,
        pageSize: 10,
        pageSizes: [5, 10, 20, 50, 100, 200, 500],
        layouts: ['Sizes', 'PrevJump', 'PrevPage', 'Number', 'NextPage', 'NextJump', 'FullJump', 'Total'],
        perfect: true,
        slots: {
          // 使用自定义插槽
          left: 'pager_left'
        }
      },
      protoco: null, // 用于详情
      protocoOption: [], // 用于详情
      protocolDefinition: [], // 用于详情
      protocolDefinition_x: [], // 用于详情
      dialogVisibleAdd: false, // 新增发布弹窗
      isImportDialogShow: false,
      isParamDialogShow: false,
      acquierValue: null,
      acquierOption: [],
      publishInfo: {},
      publicTempData: [],
      nowAcquireData: {},
      gatewayId: this.param.gatewayId,
      gatewayCode: this.param.gatewayCode,
      isShowInfo: false
    };
  },
  components: {
    VXETable,
    ImportDialog,
    paramDialog
  },
  methods: {
    // 刷新
    reLoad() {
      this.findTableList();
    },
    initAcquireSearch() {
      if (this.acquierValue) {
        this.tablePage.currentPage = 1;
        this.tablePage.pageSize = 10;
        this.tablePage.total = 0;
        this.findTableList();
      } else {
        showWarningMessage('请先选择连接！');
      }
    },
    async showAllAcquier() {
      this.acquierData = [];
      try {
        // 播放表格加载动画
        const params = {
          gatewayId: this.gatewayId
        };

        const res = await acquireListAllAcquireConfigsInShort(params);
        if (res.code === 0) {
          console.log('请求数据', res);
          this.acquierOption = [];
          this.acquierData = res.data;
          for (var i = 0; i < res.data.length; i++) {
            this.acquierOption.push({
              label: res.data[i].configName,
              value: res.data[i].acquireConfigId
            });
          }
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
        this.tableLoading = false;
      }
    },
    /**
     * @description: 分页
     * @param currentPage 第几页
     * @param pageSize 一页几条
     * @return: 无
     * @author: ZJM
     */
    onTablePageChange(currentPage, pageSize) {
      this.tablePage.currentPage = currentPage;
      this.tablePage.pageSize = pageSize;
      this.findTableList();
    },
    /**
     * @description: 获取表格数据
     * @param {null} 无
     * @return: void
     */
    async findTableList() {
      try {
        // 播放表格加载动画
        this.tableLoading = true;
        const params = {
          pageNum: this.tablePage.currentPage,
          pageSize: this.tablePage.pageSize,
          acquireConfigId: this.acquierValue
        };

        const res = await acquireListAcquirePointData(params);
        if (res.code === 0) {
          this.publicTempData = res.data;
          console.log('请求数据', res);
          this.tableDatas = res.data;
          this.tablePage.total = res.size;
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
        this.tableLoading = false;
      }
    },
    // 格式化是否为string和number
    funcType(param) {
      if (param === 'STRING') {
        return 'string';
      } else {
        return 'number';
      }
    },
    /**
     * @description:导出
     * @param {type}
     * @return:
     * @author: CH
     */
    async exportTemplate() {
      location.href = 'http://192.168.99.139:8090/match_cloud/v1/acquire/downloadPointDataTemplateExcel?acquireConfigId=' + this.acquierValue;
    },
    /**
     * @description:导入提交
     * @param {type}
     * @return:
     * @author: ZJM
     */
    submitImportFile(param) {
      const fileObj = param.file;
      const fd = new FormData();
      fd.append('file', fileObj);
      // 执行接口
      acquireUploadPointDataExcel({
        acquireConfigId: 4,
        file: fileObj
      }, fd, {
        onUploadProgress: event => {
          // eslint-disable-next-line prefer-const
          let uploadedPercentage = Math.floor(
            (event.loaded * 100) / event.total
          );
          // option.onProgress({ percent: uploadedPercentage });
          this.$refs.ImportDialog.uploadPercent = uploadedPercentage;
        }
      })
        .then(res => {
          if (res.code === 0) {
            param.onSuccess(res);
            this.findTableList();
          } else {
            console.log(111);
            param.onError(res.msg);
            this.$refs.ImportDialog.importFiles = [];
            this.$refs.ImportDialog.importFileName = '';
          }
        })
        .catch(err => {
          console.log(222);
          this.$refs.ImportDialog.importFiles = [];
          this.$refs.ImportDialog.importFileName = '';
          showWarningMessage(err);
          // param.onError(err);
        });
    },
    closeImportDialog() {
      this.isImportDialogShow = false;
      this.isParamDialogShow = false;
    },
    /**
     * @description:显示导入模态框
     * @param {type}
     * @return:
     * @author: ZJM
     */
    shoImportDialog() {
      if (!this.acquierValue) {
        showWarningMessage('请先选择连接！');
        return false;
      }
      this.isImportDialogShow = true;
      setTimeout(() => {
        this.$refs.ImportDialog.isShow = true;
        this.$refs.ImportDialog.resetUploadProgressBar();
      }, 0);
    },
    /**
     * @description:显示网关内参数
     * @param {type}
     * @return:
     * @author: ZJM
     */
    showParamDialog() {
      if (!this.acquierValue) {
        showWarningMessage('请先选择连接！');
        return false;
      }
      const proNum = this.acquierData.findIndex((item, index) => {
        return item.acquireConfigId === this.acquierValue;
      });
      this.nowAcquireData = this.acquierData[proNum];
      console.log(this.nowAcquireData);
      this.isParamDialogShow = true;
      setTimeout(() => {
        this.$refs.paramDialog.isShow = true;
        this.$refs.paramDialog.initData();
        this.$refs.paramDialog.createColumns(this.nowAcquireData, this.gatewayCode);
      }, 0);
    },
    // 显示新增模态框
    showAddParamDialog() {
      if (!this.acquierValue) {
        showWarningMessage('请先选择连接！');
        return false;
      }
      this.dialogVisibleAdd = !this.dialogVisibleAdd;
      const proNum = this.acquierData.findIndex((item, index) => {
        console.log(item.id);
        return item.acquireConfigId === this.acquierValue;
      });
      this.funcPulishParam(proNum);
    },
    funcPulishParam(param) {
      var protocolDefinition = this.acquierData[param].pointDefinition.map((item) => {
        item.nowValue = item.defaultValue;
        return item;
      });
      protocolDefinition = JSON.parse(JSON.stringify(protocolDefinition));
      for (var x = 0; x < protocolDefinition.length; x++) {
        if (protocolDefinition[x].optionList.length > 0) {
          var optionList = protocolDefinition[x].optionList;
          protocolDefinition[x].optionJson = [];
          for (var y = 0; y < optionList.length; y++) {
            protocolDefinition[x].optionJson.push({
              label: optionList[y],
              value: optionList[y]
            });
          }
        }
      }
      this.protocolDefinition = protocolDefinition;
    },
    // 隐藏模态框
    hideParamDialog() {
      this.dialogVisibleAdd = !this.dialogVisibleAdd;
    },
    // 新增发布
    async saveAdd() {
      try {
        var jsonData = {
          acquireConfigId: this.acquierValue
        };
        var jsonCon = {};
        var arr = this.protocolDefinition;
        console.log(arr);
        for (var i = 0; i < arr.length; i++) {
          jsonCon[arr[i].name] = arr[i].nowValue + '';
        }
        jsonData.pointDefinitionContentMap = jsonCon;
        console.log(jsonData);

        const res = await acquirnInsertOneAcquirePointData({}, jsonData);
        if (res.code === 0) {
          showSuccessMessage('操作成功');
          this.findTableList();
          this.dialogVisibleAdd = false;
        } else {
          // 提示异常信息
          showWarningMessage(res.msg);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
        // this.tableLoading = false;
      }
    },
    toInfo(row) {
      this.isShowInfo = true;
      this.publishInfo = row;
      const proNum = this.acquierData.findIndex((item, index) => {
        console.log(item.id);
        return item.acquireConfigId === this.acquierValue;
      });
      console.log(proNum);
      this.funcPulishParam_x(proNum);
    },
    resetUpdate() {
      this.toInfo(this.publishInfo);
    },
    funcPulishParam_x(param) {
      var protocolDefinition = this.acquierData[param].pointDefinition.map((item) => {
        item.nowValue = item.defaultValue;
        return item;
      });
      protocolDefinition = JSON.parse(JSON.stringify(protocolDefinition));
      for (var x = 0; x < protocolDefinition.length; x++) {
        if (protocolDefinition[x].optionList.length > 0) {
          var optionList = protocolDefinition[x].optionList;
          protocolDefinition[x].optionJson = [];
          for (var y = 0; y < optionList.length; y++) {
            protocolDefinition[x].optionJson.push({
              label: optionList[y],
              value: optionList[y]
            });
          }
        }
      }
      console.log(protocolDefinition, this.publishInfo);
      for (var key in this.publishInfo.pointDefinitionContent) {
        console.log(key);
        for (var i = 0; i < protocolDefinition.length; i++) {
          if (protocolDefinition[i].name === key) {
            protocolDefinition[i].nowValue = this.publishInfo.pointDefinitionContent[key];
          }
        }
      }
      console.log(protocolDefinition);
      this.protocolDefinition_x = protocolDefinition;
    },
    toUpdate() {
      this.$confirm('是否确认保存修改?', TITLE.PROMPT, {
        confirmButtonText: BTN_TEXT.CONFIRM,
        cancelButtonText: BTN_TEXT.CANCEL,
        type: 'warning'
      })
        .then(() => {
          this.saveUpdate();
        })
        .catch(() => { });
    },
    // 修改发布
    async saveUpdate() {
      try {
        var jsonData = {
          acquireConfigId: this.acquierValue,
          id: this.publishInfo.id
        };
        var jsonCon = {};
        var arr = this.protocolDefinition_x;
        for (var i = 0; i < arr.length; i++) {
          jsonCon[arr[i].name] = arr[i].nowValue + '';
        }
        jsonData.pointDefinitionContentMap = jsonCon;
        console.log(jsonData);

        const res = await acquireUpdateAcquirePointData({}, jsonData);
        if (res.code === 0) {
          showSuccessMessage('操作成功');
          this.findTableList();
        } else {
          // 提示异常信息
          showWarningMessage(res.msg);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
        // this.tableLoading = false;
      }
    },
    toDeleteAcquire(row) {
      this.$confirm('是否确认删除?', TITLE.PROMPT, {
        confirmButtonText: BTN_TEXT.CONFIRM,
        cancelButtonText: BTN_TEXT.CANCEL,
        type: 'warning'
      })
        .then(() => {
          this.deleteAcquire(row);
        })
        .catch(() => { });
    },
    // 执行删除
    async deleteAcquire(row) {
      try {
        // 播放表格加载动画
        this.tableLoading = true;
        const params = [row.id];
        const res = await acquireDeleteAcquirePointData({}, params);
        if (res.code === 0) {
          showSuccessMessage('操作成功');
          this.findTableList();
        } else {
          // 提示异常信息
          showWarningMessage(res.msg);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
        this.tableLoading = false;
      }
    },
    // 判断是否执行网关同步到平台
    toCloud() {
      if (!this.acquierValue) {
        showWarningMessage('请先选择连接！');
        return false;
      }
      this.$confirm('是否确认网关同步到平台?', TITLE.PROMPT, {
        confirmButtonText: BTN_TEXT.CONFIRM,
        cancelButtonText: BTN_TEXT.CANCEL,
        type: 'warning'
      })
        .then(() => {
          this.doToCloud();
        })
        .catch(() => { });
    },
    // 执行网关同步到平台
    async doToCloud() {
      try {
        // 播放表格加载动画
        const params = {
          acquireConfigId: this.acquierValue,
          gatewayCode: this.gatewayCode,
          gatewayId: this.gatewayId
        };
        const res = await acquirePointDataOverwriteToCloud(params);
        if (res.code === 0) {
          showSuccessMessage('操作成功');
          this.initAcquireSearch();
        } else {
          // 提示异常信息
          showWarningMessage(res.msg);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
        this.tableLoading = false;
      }
    },
    // 判断是否执行平台同步到网关
    toEdge() {
      if (!this.acquierValue) {
        showWarningMessage('请先选择连接！');
        return false;
      }
      this.$confirm('是否确认网关同步到平台?', TITLE.PROMPT, {
        confirmButtonText: BTN_TEXT.CONFIRM,
        cancelButtonText: BTN_TEXT.CANCEL,
        type: 'warning'
      })
        .then(() => {
          this.doToEdge();
        })
        .catch(() => { });
    },
    // 执行平台同步到网关
    async doToEdge() {
      try {
        // 播放表格加载动画
        const params = {
          acquireConfigId: this.acquierValue,
          gatewayCode: this.gatewayCode,
          gatewayId: this.gatewayId
        };
        const res = await acquirePointDataOverwriteToEdge(params);
        if (res.code === 0) {
          showSuccessMessage('操作成功');
          this.initAcquireSearch();
        } else {
          // 提示异常信息
          showWarningMessage(res.msg);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
        this.tableLoading = false;
      }
    }
  },
  created() {
    // this.findTableList();
    // this.showPulishTemp();
    // 查询所有连接
    this.showAllAcquier();
  }
};
</script>

<style lang="less" scoped>
.leftTask{
  float: left;
  width: 75%;
  padding: 10px;
}
.rightTask{
  float: left;
  width: calc( 25% - 10px  );
  padding: 10px 5px;
  height: calc( 100vh - 200px );
}
.form1{
  padding: 20px 5px 5px 10px;
}
.form2{
  padding: 20px 5px 0px 10px;
  border-top: 1px solid #ddd;
}
.rightTaskCon{
  border:1px solid #ddd;
  width: 100%;
  height: 100%;
  text-align: center;
  overflow-y: auto;
  overflow-x: hidden;
}
.rightTaskCon2{
  width: 100%;
  height: 100%;
