<!--
 * @Descripttion: 发布配置
 * @version:
 * @Author: ZJM
 * @Date: 2022-05-25 14:53:53
 * @LastEditors: ZJM
 * @LastEditTime: 2022-05-26 15:08:01
-->
<template>
  <!-- <h1>网关信息</h1> -->
  <div>
    <div class="leftTask" >
      <vxe-grid
        border="full"
        resizable
        keep-source
        ref="xGrid"
        class="vxe-table-element"
        :height="$options.filters.getTableHeight(200)"
        highlight-current-row
        :loading="tableLoading"
        :columns="tableColumns"
        :data="tableDatas"
      ></vxe-grid>
    </div>
    <div v-if="protocoOption_x.length>0"  class="rightTask" >
      <div class="rightTaskCon" >
        <el-form class="form1" ref="editCodeForm">
        <el-row :gutter="22">
          <el-col :span="22">
            <el-form-item
            :label="'发布名称:'"
            :label-width="formLabelWidth">
              <el-input v-model="publishName_x" ></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="22">
            <el-form-item
            :label="'发布类型:'"
            :label-width="formLabelWidth"  >
              <el-select
                v-model="protoco_x"
                style="width: 100%"
                :placeholder="VARIABLE.PLEASE_CHOOSE"
              >
               <el-option
                  v-for="item in protocoOption_x"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          </el-row >
        </el-form>
       <el-form class="form2" ref="editCodeForm">
        <el-row :gutter="22">
          <el-col v-for="(item,index) in protocolDefinition_x" :key="index" :span="22">
            <el-form-item
            :label="item.explaination"
            :label-width="formLabelWidth"  >

              <el-input
               v-if="item.optionList.length===0"
               v-model="item.nowValue"
               :type=funcType(item.dataTypeEnum)
              ></el-input>

              <el-select
                v-if="item.optionList.length>0"
                v-model="item.nowValue"
                style="width: 100%"
                :placeholder="VARIABLE.PLEASE_CHOOSE"
              >
                <el-option
                  v-for="itemx in item.optionJson"
                  :key="itemx.value"
                  :label="itemx.label"
                  :value="itemx.value"
                ></el-option>
              </el-select>

            </el-form-item>
          </el-col>
          </el-row >
        </el-form>
        <el-button type="primary" size="mini" @click="toUpdate()" >{{BTN_TEXT.SAVE+'修改'}}</el-button>
       <el-button size="mini" @click="resetUpdate()" >{{BTN_TEXT.CANCEL}}</el-button>
      </div>
    </div>

    <el-dialog
      :class="[$store.state.dialogMarginClass]"
      :title="'新增发布'"
      :width="'550px'"
      :visible.sync="dialogVisibleAdd"
    >
        <div class="rightTaskCon2" >
        <el-form class="form1" ref="editCodeForm">
        <el-row :gutter="22">
          <el-col :span="22">
            <el-form-item
            :label="'发布名称:'"
            :label-width="formLabelWidth">
              <el-input v-model="publishName" ></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="22">
            <el-form-item
            :label="'发布类型:'"
            :label-width="formLabelWidth"  >
              <el-select
                v-model="protoco"
                style="width: 100%"
                :placeholder="VARIABLE.PLEASE_CHOOSE"
                @change="selectChangeAdd"
              >
               <el-option
                  v-for="item in protocoOption"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          </el-row >
        </el-form>
       <el-form class="form2" ref="editCodeForm">
        <el-row :gutter="22">
          <el-col v-for="(item,index) in protocolDefinition" :key="index" :span="22">
            <el-form-item
            :label="item.explaination"
            :label-width="formLabelWidth"  >

              <el-input
               v-if="item.optionList.length===0"
               v-model="item.nowValue"
               :type=funcType(item.dataTypeEnum)
              ></el-input>

              <el-select
                v-if="item.optionList.length>0"
                v-model="item.nowValue"
                style="width: 100%"
                :placeholder="VARIABLE.PLEASE_CHOOSE"
              >
                <el-option
                  v-for="itemx in item.optionJson"
                  :key="itemx.value"
                  :label="itemx.label"
                  :value="itemx.value"
                ></el-option>
              </el-select>

            </el-form-item>
          </el-col>
          </el-row >
        </el-form>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" size="mini" @click="saveAdd()" >{{BTN_TEXT.SAVE}}</el-button>
        <el-button size="mini" @click="hidePublish()"  >{{BTN_TEXT.CANCEL}}</el-button>
      </span>
    </el-dialog>

  </div>
</template>

<script>
// // 导入title
// import HeaderTitle from '@/components/common/PageNavBar/HeaderTitle';
// import HeadOperationItem from '@/components/common/PageNavBar/HeadOperationItem';
// import VXETable from '@/components/common/Table';
// import PageAuthoritySetting from '@/views/main/RoleManagement/PageAuthoritySetting';
import {
  TITLE,
  VARIABLE,
  BTN_TEXT
} from '@/utils/constantsFile';
import {
  publishTemplateListPublishTemplatesInShort,
  publishListCombinedPublishConfigs,
  bizLayDownPublishConfig,
  publishDeleteCloudPublishConfig,
  publishDeleteEdgePublishConfig,
  publishInsertPublishConfig,
  publishUpdatePublishConfig
} from '@/api/publishConfig';
import { showWarningMessage, showSuccessMessage } from '@/utils/message';
import { showErrorNotify } from '@/utils/notify';
export default {
  name: 'publishConfig',
  props: {
    param: {
      type: Object,
      default: () => {
        return {
          gatewayId: null,
          gatewayCode: null
        };
      }
    }
  },
  data() {
    return {
      BTN_TEXT: BTN_TEXT,
      VARIABLE: VARIABLE,
      // gatewayCode: '123',
      // publishConfigId: 2,
      tableLoading: false,
      // 模态框输入框的宽度
      formLabelWidth: '100px',
      // 表格字段
      tableDatas: [],
      // 表头
      tableColumns: [
        {
          title: '序号',
          minWidth: '10',
          align: 'center',
          type: 'seq'
        },
        {
          field: 'configName',
          title: '发布名称',
          align: 'center'
          // sortable: true
        },
        {
          field: 'protocolName',
          title: '发布类型',
          align: 'center'
          // formatter: this.formatUser
          // sortable: true
        },
        {
          field: 'topic',
          title: 'topic',
          align: 'center',
          slots: {
            default: ({ row, column }) => {
              return row.protocolDefinitionContent.topic;
            }
          }
          // sortable: true
        },
        {
          field: 'qos',
          title: 'qos',
          align: 'center',
          slots: {
            default: ({ row, column }) => {
              return row.protocolDefinitionContent.qos;
            }
          }
          // sortable: true
        },
        {
          field: 'configStatusEnum',
          title: '生效状态',
          minWidth: '80',
          align: 'center',
          slots: {
            default: ({ row, column }) => {
              if (row.configStatusEnum === 'NOT_IN_USE') {
                return [<span class="noPass" >未同步</span>];
              } else {
                return [<span class="pass" >已生效</span>];
              }
            }
          }
          // sortable: true
        },
        {
          field: 'linkTime',
          title: '上次通信时间',
          align: 'center',
          slots: {
            default: ({ row, column }) => {
              if (row.linkTime) {
                return this.$options.filters.formatTime(row.linkTime);
              } else {
                return '';
              }
            }
          }
          // sortable: true
        },
        {
          title: '操作',
          width: '300',
          align: 'center',
          fixed: 'right',
          slots: {
            default: ({ row, column }) => {
              if (row.configStatusEnum === 'NOT_IN_USE') {
                return [
                  <vxe-button type="text" status="primary" content="详情" onClick={() => this.toInfo(row, column)} ></vxe-button>,
                  <vxe-button type="text" status="primary" content="同步到网关" onClick={() => this.toSynchronousNetWork(row, column)} ></vxe-button>,
                  <vxe-button type="text" status="danger" content="删除" onClick={() => this.toDeletePublish(row, column)} ></vxe-button>
                ];
              } else {
                return [
                  <vxe-button type="text" status="primary" content="详情" onClick={() => this.toInfo(row, column)} ></vxe-button>,
                  <vxe-button type="text" status="danger" content="远程删除" onClick={() => this.toDeleteEdgePublish(row, column)} ></vxe-button>
                ];
              }
            }
          }
        }
      ],
      tablePage: {
        total: 0,
        currentPage: 1,
        pageSize: 10,
        pageSizes: [5, 10, 20, 50, 100, 200, 500],
        layouts: ['Sizes', 'PrevJump', 'PrevPage', 'Number', 'NextPage', 'NextJump', 'FullJump', 'Total'],
        perfect: true,
        slots: {
          // 使用自定义插槽
          left: 'pager_left'
        }
      },
      gatewayId: this.param.gatewayId, // 网关id
      gatewayCode: this.param.gatewayCode, // 网关code
      publishName: '',
      publishName_x: '',
      protoco: null,
      protocoOption: [],
      protocolDefinition: [],
      protoco_x: null,
      protocoOption_x: [],
      protocolDefinition_x: [],
      dialogVisibleAdd: false,
      publishData: [],
      publicTempData: [],
      publicTempData_x: [],
      publishInfo: {}
    };
  },
  components: {
    // HeaderTitle,
    // HeadOperationItem,
    // VXETable,
    // PageAuthoritySetting
  },
  methods: {
    // 刷新
    reLoad() {
      this.findTableList();
    },
    /**
     * @description: 获取表格数据
     * @param {null} 无
     * @return: void
     */
    async findTableList() {
      try {
        // 播放表格加载动画
        this.tableLoading = true;
        const params = {
          gatewayCode: this.gatewayCode,
          gatewayId: this.gatewayId
        };

        const res = await publishListCombinedPublishConfigs(params);
        if (res.code === 0) {
          console.log('请求数据', res);
          this.tableDatas = res.data;
        } else {
          // 提示异常信息
          showWarningMessage(res.msg);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
        this.tableLoading = false;
      }
    },
    // 获取发布类型模板
    showPulishTemp() {
      publishTemplateListPublishTemplatesInShort()
        .then(res => {
          console.log(res.data);
          this.protocoOption = [];
          this.publicTempData = res.data;
          for (var i = 0; i < res.data.length; i++) {
            this.protocoOption.push({
              label: res.data[i].protocolName,
              value: res.data[i].id
            });
          }
          this.funcPulishParam(0);
          this.protoco = res.data[0].id;
        });
    },
    showPulishTemp_x() {
      publishTemplateListPublishTemplatesInShort()
        .then(res => {
          console.log(res.data);
          this.protocoOption_x = [];
          this.publicTempData_x = res.data;
          for (var i = 0; i < res.data.length; i++) {
            this.protocoOption_x.push({
              label: res.data[i].protocolName,
              value: res.data[i].id
            });
          }
          if (this.publicTempData_x[0].id !== res.data[0].id) {
            this.protoco_x = this.publicTempData_x[0].id;
            this.selectChange_x(this.publicTempData_x[0].id);
          } else {
            this.funcPulishParam_x(0);
            this.protoco_x = res.data[0].id;
          }
        });
    },
    // 处理选完发布类型后自动生成其对应参数
    funcPulishParam(param) {
      var protocolDefinition = this.publicTempData[param].protocolDefinition.map((item) => {
        item.nowValue = item.defaultValue;
        return item;
      });
      protocolDefinition = JSON.parse(JSON.stringify(protocolDefinition));
      for (var x = 0; x < protocolDefinition.length; x++) {
        if (protocolDefinition[x].optionList.length > 0) {
          var optionList = protocolDefinition[x].optionList;
          protocolDefinition[x].optionJson = [];
          for (var y = 0; y < optionList.length; y++) {
            protocolDefinition[x].optionJson.push({
              label: optionList[y],
              value: optionList[y]
            });
          }
        }
      }
      console.log(protocolDefinition);
      this.protocolDefinition = protocolDefinition;
    },
    funcPulishParam_x(param) {
      var protocolDefinition = this.publicTempData_x[param].protocolDefinition.map((item) => {
        item.nowValue = item.defaultValue;
        return item;
      });
      protocolDefinition = JSON.parse(JSON.stringify(protocolDefinition));
      for (var x = 0; x < protocolDefinition.length; x++) {
        if (protocolDefinition[x].optionList.length > 0) {
          var optionList = protocolDefinition[x].optionList;
          protocolDefinition[x].optionJson = [];
          for (var y = 0; y < optionList.length; y++) {
            protocolDefinition[x].optionJson.push({
              label: optionList[y],
              value: optionList[y]
            });
          }
        }
      }
      console.log(protocolDefinition);
      for (var key in this.publishInfo.protocolDefinitionContent) {
        console.log(key);
        for (var i = 0; i < protocolDefinition.length; i++) {
          if (protocolDefinition[i].name === key) {
            protocolDefinition[i].nowValue = this.publishInfo.protocolDefinitionContent[key];
          }
        }
      }
      this.publishName_x = this.publishInfo.configName;
      this.protocolDefinition_x = protocolDefinition;
    },
    // 新增是下拉选类型事件
    selectChangeAdd(value) {
      console.log(value);
      const proNum = this.publicTempData.findIndex((item, index) => {
        console.log(item.id);
        return item.id === value;
      });
      console.log(proNum);
      if (proNum === -1) {
        this.protocolDefinition = [];
      } else {
        this.funcPulishParam(proNum);
      }
    },
    // 新增是下拉选类型事件
    selectChange_x(value) {
      console.log(value);
      const proNum = this.publicTempData.findIndex((item, index) => {
        console.log(item.id);
        return item.id === value;
      });
      console.log(proNum);
      if (proNum === -1) {
        this.protocolDefinition = [];
      } else {
        this.funcPulishParam(proNum);
      }
    },
    // 新增发布
    async saveAdd() {
      try {
        var jsonData = {
          configName: this.publishName,
          gatewayId: this.gatewayId,
          templateId: this.protoco
        };
        var jsonCon = {};
        var arr = this.protocolDefinition;
        for (var i = 0; i < arr.length; i++) {
          jsonCon[arr[i].name] = arr[i].nowValue + '';
        }
        jsonData.protocolDefinitionContent = JSON.stringify(jsonCon);
        console.log(jsonData);

        const res = await publishInsertPublishConfig(jsonData);
        if (res.code === 0) {
          showSuccessMessage('操作成功');
          this.findTableList();
          this.dialogVisibleAdd = false;
        } else {
          // 提示异常信息
          showWarningMessage(res.msg);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
        // this.tableLoading = false;
      }
    },
    // 格式化是否为string和number
    funcType(param) {
      if (param === 'STRING') {
        return 'string';
      } else {
        return 'number';
      }
    },
    // 显示新增模态框
    addPublish() {
      this.showPulishTemp();
      this.dialogVisibleAdd = !this.dialogVisibleAdd;
    },
    // 隐藏模态框
    hidePublish() {
      this.dialogVisibleAdd = !this.dialogVisibleAdd;
    },
    // 判断是否同步到网关(下放发布配置)
    toSynchronousNetWork(row) {
      this.$confirm('是否确认同步到网关?', TITLE.PROMPT, {
        confirmButtonText: BTN_TEXT.CONFIRM,
        cancelButtonText: BTN_TEXT.CANCEL,
        type: 'warning'
      })
        .then(() => {
          this.synchronousNetWork(row);
        })
        .catch(() => { });
    },
    // 同步到网关(下放发布配置)
    async synchronousNetWork(row) {
      try {
        // 播放表格加载动画
        this.tableLoading = true;
        const params = {
          gatewayCode: this.gatewayCode,
          publishConfigId: row.id
        };

        const res = await bizLayDownPublishConfig(params);
        if (res.code === 0) {
          showSuccessMessage('操作成功');
          this.findTableList();
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
        this.tableLoading = false;
      }
    },
    // 判断是否删除
    toDeletePublish(row) {
      this.$confirm('是否确认删除?', TITLE.PROMPT, {
        confirmButtonText: BTN_TEXT.CONFIRM,
        cancelButtonText: BTN_TEXT.CANCEL,
        type: 'warning'
      })
        .then(() => {
          this.deletePublish(row);
        })
        .catch(() => { });
    },
    // 执行删除
    async deletePublish(row) {
      try {
        // 播放表格加载动画
        this.tableLoading = true;
        const params = {
          gatewayId: row.gatewayId,
          id: row.id
        };

        const res = await publishDeleteCloudPublishConfig(params);
        if (res.code === 0) {
          showSuccessMessage('操作成功');
          this.findTableList();
        } else {
          // 提示异常信息
          showWarningMessage(res.msg);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
        // this.tableLoading = false;
      }
    },
    // 判断是否远程删除
    toDeleteEdgePublish(row) {
      this.$confirm('是否确认远程删除?', TITLE.PROMPT, {
        confirmButtonText: BTN_TEXT.CONFIRM,
        cancelButtonText: BTN_TEXT.CANCEL,
        type: 'warning'
      })
        .then(() => {
          this.deleteEdgePublish(row);
        })
        .catch(() => { });
    },
    // 执行远程删除
    async deleteEdgePublish(row) {
      try {
        // 播放表格加载动画
        this.tableLoading = true;
        const params = {
          gatewayId: row.gatewayId,
          gatewayCode: this.gatewayCode,
          id: row.id
        };

        const res = await publishDeleteEdgePublishConfig(params);
        if (res.code === 0) {
          showSuccessMessage('操作成功');
          this.findTableList();
        } else {
          // 提示异常信息
          showWarningMessage(res.msg);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
        this.tableLoading = false;
      }
    },
    toInfo(row) {
      this.publishInfo = row;
      this.showPulishTemp_x();
      console.log(row);
    },
    resetUpdate() {
      this.toInfo(this.publishInfo);
    },
    toUpdate() {
      this.$confirm('是否确认保存修改?', TITLE.PROMPT, {
        confirmButtonText: BTN_TEXT.CONFIRM,
        cancelButtonText: BTN_TEXT.CANCEL,
        type: 'warning'
      })
        .then(() => {
          this.saveUpdate();
        })
        .catch(() => { });
    },
    // 修改发布
    async saveUpdate() {
      try {
        var jsonData = {
          configName: this.publishName_x,
          gatewayId: this.gatewayId,
          templateId: this.protoco_x,
          id: this.publishInfo.id
        };
        var jsonCon = {};
        var arr = this.protocolDefinition_x;
        for (var i = 0; i < arr.length; i++) {
          jsonCon[arr[i].name] = arr[i].nowValue + '';
        }
        jsonData.protocolDefinitionContent = JSON.stringify(jsonCon);
        console.log(jsonData);

        const res = await publishUpdatePublishConfig({}, jsonData);
        if (res.code === 0) {
          showSuccessMessage('操作成功');
          this.findTableList();
        } else {
          // 提示异常信息
          showWarningMessage(res.msg);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
        // this.tableLoading = false;
      }
    }
  },
  created() {
    this.findTableList();
  }
};

</script>

<style>
.leftTask{
  float: left;
  width: 75%;
  padding: 10px;
}
.rightTask{
  float: left;
  width: calc( 25% - 10px  );
  padding: 10px 5px;
  height: calc( 100vh - 180px );
}
.form1{
  padding: 20px 5px 5px 10px;
}
.form2{
  padding: 20px 5px 0px 10px;
  border-top: 1px solid #ddd;
}
.rightTaskCon{
  border:1px solid #ddd;
  width: 100%;
  height: 100%;
  text-align: center;
    overflow-y: auto;
  overflow-x: hidden;
}
.rightTaskCon2{
  width: 100%;
  height: 100%;
  text-align: center;
}
.pass{
  background:#CAFBEB;
  padding: 5px;
  border-radius: 10px;
  font-weight: bold;
}
.noPass{
  background:#D5D5D5;
  padding: 5px;
  border-radius: 10px;
  font-weight: bold;
}
/* /deep/ .el-dialog__body {
  padding: 0px 0px;
} */

</style>
