<template>
  <div class="MaskList network">
    <div class="table_con">
      <vxe-grid
        border="full"
        resizable
        keep-source
        ref="xGrid"
        class="vxe-table-element"
        :max-height="$options.filters.getTableHeight(200)"
        highlight-current-row
        :loading="tableLoading"
        :columns="tableColumns"
        :data="tableDatas"
      ></vxe-grid>
    </div>
    <div class="form_con network_form card_module" v-if="detailVisible">
      <form-module
        :line="false"
        :formData="detailFormData"
        :formOptions="detailFormOptions"
      >
        <template v-slot:formItemSlot>
          <el-button type="primary" class="w100" @click="handleAnnounced">待发布内容</el-button>
        </template>
        <template v-slot:footer>
          <div class="ta-c m-t80">
            <el-button size="mini" type="primary" class="m-r30" @click="handleSubmit('保存修改')">保存修改</el-button>
            <el-button size="mini" @click="handleSubmit('取消保存')">取消</el-button>
          </div>
        </template>
      </form-module>
    </div>
    <el-dialog
      :class="[$store.state.dialogMarginClass]"
      :title="'新增任务'"
      :width="'450px'"
      :visible.sync="addDialogVisible"
    >
      <div class="dialog_con">
        <form-module
          :line="false"
          :formData="dialogFormData"
          :formOptions="dialogFormOptions"
        >
          <template v-slot:footer>
            <div class="ta-c m-t35">
              <el-button size="mini" type="primary" class="m-r30" @click="handleSubmit('新增任务')">确认</el-button>
              <el-button size="mini" @click="addDialogVisible = false">取消</el-button>
            </div>
          </template>
        </form-module>
      </div>
    </el-dialog>
    <el-dialog
      :class="[$store.state.dialogMarginClass]"
      :title="'编辑待发布内容'"
      :width="'800px'"
      custom-class="issueDialog"
      :visible.sync="issueDialogVisible"
    >
      <div class="issue_wra">
        <el-button size="mini" type="primary" class="m-r30" @click="handleEditParam">编辑待发布参数</el-button>
        <div class="issue_con">
          <div class="card_module param_con">
            <div class="header">
              <div class="title">待发布采集参数</div>
            </div>
            <div class="param_list">
              <p v-for="(item, index) in paramList" :key="index">{{ item.name }}</p>
            </div>
          </div>
          <div class="script_con card_module">
          <div class="header">
            <div class="title">脚本编辑器(lua)</div>
          </div>
          <div class="script_list">
            <el-input
              type="textarea"
              :autosize="{ minRows: 16, maxRows: 16}"
              v-model="scriptContent">
            </el-input>
          </div>
        </div>
        </div>
      </div>
      <div class="ta-c m-t35">
        <el-button size="mini" type="primary" class="m-r30" @click="issueDialogVisible = false">保存修改</el-button>
        <el-button size="mini" @click="issueDialogVisible = false">取消</el-button>
      </div>
    </el-dialog>
    <el-dialog
      :class="[$store.state.dialogMarginClass]"
      :title="'编辑待发布参数'"
      :width="'600px'"
      :visible.sync="paramDialogVisible"
    >
      <div class="editParam_con">
        <div class="addParam">
          <el-button size="mini" type="primary" class="addBtn" @click="handleAddParam">新增计算参数</el-button>
        </div>
       <el-table
          ref="multipleTable"
          :data="paramTableData"
          border
          :header-cell-style="{background: '#eff0f2', color: '#606266'}"
          max-height="500"
          style="width: 100%"
          @selection-change="handleSelectionChange">
          <el-table-column
            type="selection"
            align="center"
            width="40">
          </el-table-column>
          <el-table-column
            prop="pointName"
            align="center"
            label="参数名称">
          </el-table-column>
          <el-table-column
            prop="pointAbbrName"
            align="center"
            label="参数别名">
          </el-table-column>
          <el-table-column
            prop="dataType"
            align="center"
            label="参数类型">
          </el-table-column>
        </el-table>
      </div>
      <div class="ta-c m-t20">
        <el-button size="mini" type="primary" class="m-r30" @click="handleSubmit('编辑待发布参数')">确认</el-button>
        <el-button size="mini" @click="paramDialogVisible = false">取消</el-button>
      </div>
    </el-dialog>
    <el-dialog
      :class="[$store.state.dialogMarginClass]"
      :title="'新增计算参数'"
      :width="'450px'"
      :visible.sync="addParamDialogVisible"
    >
      <div class="dialog_con">
       <!--  <form-module
          :line="false"
          :formData="addParamFormData"
          :formOptions="addParamFormOptions"
        >
          <template v-slot:footer>
            <div class="ta-c m-t35">
              <el-button size="mini" type="primary" class="m-r30" @click="handleSubmit('新增计算参数')">确认</el-button>
              <el-button size="mini" @click="addParamDialogVisible = false">取消</el-button>
            </div>
          </template>
        </form-module> -->
      </div>
    </el-dialog>
  </div>
</template>

<script>
// import FormModule from '../FormModule';
import {
  listCombinedTasks,
  insertGatewayTaskName,
  listAllAcquireConfigsInShort,
  listAllPublishConfigsInShort,
  updateGatewayTask,
  deleteCloudGatewayTask,
  deleteEdgeTask,
  listGatewayTasks,
  listUsedPointData
} from '@/api/publishConfig';
const Base64 = require('js-base64').Base64;
export default {
  name: 'MaskList',
  components: {
    // FormModule
  },
  props: {
    param: {
      type: Object,
      default: () => {
        return {
          gatewayId: null,
          gatewayCode: null
        };
      }
    }
  },
  data() {
    return {
      addDialogVisible: false,
      issueDialogVisible: false,
      paramDialogVisible: false,
      addParamDialogVisible: false,
      tableLoading: false,
      detailVisible: false,
      scriptContent: '',
      currentRow: {},
      detailFormData: {},
      dialogFormData: {},
      addParamFormData: {},
      paramList: [],
      detailFormOptions: [{
        key: 'taskName',
        label: '任务名:'
      }, {
        type: 'select',
        key: 'acquireConfigCode',
        label: '连接:',
        options: []
      }, {
        type: 'button',
        key: 'button',
        label: ''
      }, {
        type: 'select',
        key: 'publishConfigCode',
        label: '发布',
        options: []
      }],
      dialogFormOptions: [{
        key: 'taskName',
        label: '任务名:'
      }, {
        type: 'select',
        key: 'acquireConfigCode',
        label: '连接:',
        options: []
      }, {
        type: 'select',
        key: 'publishConfigCode',
        label: '发布:',
        options: []
      }],
      addParamFormOptions: [{
        key: 'pointName',
        label: '参数名称:'
      }, {
        key: 'pointAbbrName',
        label: '参数别名:'
      }, {
        type: 'select',
        key: 'dataType',
        label: '参数类型:',
        options: [{
          label: 'INT',
          value: 'INT'
        }, {
          label: 'FLOAT',
          value: 'FLOAT'
        }, {
          label: 'STRING',
          value: 'STRING'
        }]
      }],
      // 表头
      tableColumns: [
        {
          type: 'seq',
          field: '',
          title: '序号',
          width: '80',
          align: 'center'
        },
        {
          field: 'taskName',
          title: '任务名称',
          align: 'center'
        },
        {
          field: 'taskStatusEnum',
          title: '生效状态',
          // minWidth: '80',
          align: 'center',
          slots: {
            default: ({ row, column }) => {
              if (row.taskStatusEnum === 'IN_USE') {
                return [<span class="sync takeEffect" >已生效</span>];
              } else if (row.taskStatusEnum === 'NOT_IN_USE') {
                return [<span class="noSync takeEffect" >未同步</span>];
              }
            }
          }
        },
        {
          field: 'linkTime',
          title: '上次通信时间',
          // minWidth: '100',
          align: 'center',
          formatter: ['formatDate', 'yyyy-MM-dd HH:mm:ss']
        },
        {
          title: '操作',
          // width: '300',
          align: 'center',
          fixed: 'right',
          slots: {
            default: ({ row, column }) => {
              const list = [
                <vxe-button type="text" status="primary" content="详情" onClick={() => this.handleDatial(row, column)}></vxe-button>
              ];
              if (row.taskStatusEnum === 'NOT_IN_USE') {
                list.splice(1, 0, <vxe-button type="text" status="primary" content="连接" onClick={() => this.handleConcat(row, column)}></vxe-button>);
                list.splice(2, 0, <vxe-button type="text" status="danger" content="删除" onClick={() => this.handleDel(row, deleteCloudGatewayTask, '删除')} ></vxe-button>);
              } else {
                list.splice(1, 0, <vxe-button type="text" status="danger" content="远程删除" onClick={() => this.handleDel(row, deleteEdgeTask, '远程删除')}></vxe-button>);
              }
              return list;
            }
          }
        }
      ],
      // 表格字段
      tableDatas: [],
      paramTableData: [{
        date: '2016-05-03',
        name: '王小虎',
        address: '上海市普陀区金沙江路'
      }],
      selectedParamList: []
    };
  },
  created() {
    this.loadData();
    this.loadSelect();
  },
  methods: {
    // /刷新
    reLoad() {
      this.loadData();
    },
    // 进入时加载
    loadData() {
      this.tableLoading = true;
      listCombinedTasks({
        gatewayCode: this.param.gatewayCode,
        gatewayId: this.param.gatewayId
      }).then(res => {
        this.tableDatas = res.data || [];
        this.tableLoading = false;
      });
    },
    // 加载下拉框数据
    loadSelect() {
      // 连接下拉框
      listAllAcquireConfigsInShort({
        gatewayId: this.param.gatewayId
      }).then(res => {
        const temp = res.data?.map(item => {
          return {
            label: item.configName,
            value: item.configCode
          };
        });
        this.detailFormOptions[1].options = temp;
        this.dialogFormOptions[1].options = temp;
      });
      // 发布下拉框
      listAllPublishConfigsInShort({
        gatewayId: this.param.gatewayId
      }).then(res => {
        const temp = res.data?.map(item => {
          return {
            label: item.configName,
            value: item.configCode
          };
        });
        this.detailFormOptions[3].options = temp;
        this.dialogFormOptions[2].options = temp;
      });
    },
    // 新增任务
    handleAddMask() {
      this.addDialogVisible = true;
    },
    // 待发布内容
    handleAnnounced() {
      listGatewayTasks({
        gatewayId: this.param.gatewayId,
        pageNum: 1,
        pageSize: 1,
        taskId: this.currentRow.id,
        taskName: this.currentRow.taskName
      }).then(res => {
        const { taskContent: { outParams = [], luaScript } } = res.data[0] || {};
        this.paramList = outParams;
        this.scriptContent = Base64.decode(luaScript);
      });
      this.issueDialogVisible = true;
    },
    // 打开编辑参数dialog
    handleEditParam() {
      this.paramDialogVisible = true;
      listUsedPointData({
        acquireConfigCode: this.detailFormData.acquireConfigCode,
        gatewayId: this.param.gatewayId
      }).then(res => {
        this.paramTableData = res.data || [];
        const rows = this.paramTableData.filter(item => this.paramList.some(v => item.pointName === v.name));
        console.log(this.$refs.multipleTable);
        this.$nextTick(() => {
          rows.forEach(row => {
            this.$refs.multipleTable.toggleRowSelection(row, true);
          });
        });
      });
    },
    // 参数dialog多选
    handleSelectionChange(val) {
      this.selectedParamList = val;
    },
    // 打开新增参数dialog
    handleAddParam() {
      this.addParamDialogVisible = true;
      Object.keys(this.addParamFormData).forEach(item => {
        this.addParamFormData[item] = '';
      });
    },
    // 点击详情
    handleDatial(row) {
      this.currentRow = row;
      const { taskName, taskContent: { acquireConfigCodes, publishConfigCodes } } = row;
      this.detailFormData = {
        taskName,
        acquireConfigCode: acquireConfigCodes?.[0],
        publishConfigCode: publishConfigCodes?.[0]
      };
      this.detailVisible = true;
    },
    // 提交表单
    handleSubmit(name) {
      switch (name) {
        case '新增任务':
          insertGatewayTaskName({
            ...this.dialogFormData,
            gatewayId: this.param.gatewayId
          }).then(res => {
            this.addDialogVisible = false;
            this.loadData();
            this.$message({
              message: '新增成功',
              type: 'success'
            });
          });
          break;
        case '保存修改':
          // eslint-disable-next-line no-case-declarations
          const { taskName, acquireConfigCode, publishConfigCode } = this.detailFormData;
          updateGatewayTask({}, {
            gatewayId: this.param.gatewayId,
            id: 10,
            taskName,
            taskContent: {
              acquireConfigCodes: [acquireConfigCode],
              publishConfigCodes: [publishConfigCode],
              luaScript: Base64.encode(this.scriptContent),
              outParams: this.paramList
            }
          }).then(res => {
            this.loadData();
            this.$message({
              message: '保存成功',
              type: 'success'
            });
          });
          break;
        case '取消保存':
          this.handleDatial(this.currentRow);
          break;
        case '新增计算参数':
          this.paramTableData.unshift(this.addParamFormData);
          this.addParamDialogVisible = false;
          break;
        case '编辑待发布参数':
          this.paramList = this.selectedParamList.map(item => {
            return {
              name: item.pointName,
              abbrName: item.pointAbbrName,
              typeEnum: item.dataType
            };
          });
          this.paramDialogVisible = false;
          break;
      }
    },
    // 删除 / 远程删除
    handleDel({ id }, apiName, type) {
      apiName({
        gatewayId: this.param.gatewayId,
        id,
        gatewayCode: type === '远程删除' ? this.param.gatewayCode : undefined
      }).then(res => {
        this.loadData();
        this.$message({
          message: '删除成功',
          type: 'success'
        });
      });
    }
  }
};
</script>

<style lang="less" scoped>
  .MaskList {
    display: flex;
    justify-content: space-between;
    height: 100%;
    padding: 10px;

    .table_con {
      width: 74%;
      height: 100%;

      .takeEffect {
        display: inline-block;
        width: 70px;
        height: 28px;
        line-height: 28px;
        // font-weight: bold;
        color: #000000;
        border-radius: 14px;
      }
      .sync {
        background: #CAFBEB;
      }
      .noSync {
        background: #D5D5D5;
      }
    }
    .form_con {
      position: relative;
      width: 25%;
      height: 400px;
      padding: 25px 50px;
    }
  }

  .dialog_con {
    padding: 0 30px;
  }
  .issue_con {
    display: flex;
    justify-content: space-between;
    min-height: 400px;
    margin-top: 12px;

     .header {
        padding: 10px;
        border-bottom: 1px solid #D5D5D5;
      }
    .param_con {
      width: 25%;
      background-color: #ffffff;

      .param_list {
        padding: 10px 20px;
        font-weight: bold;
        color: #333333;
      }
    }
    .script_con {
      width: 73%;
      background-color: #ffffff;
      .script_list {
        padding: 10px;
      }
    }
  }
  .editParam_con {
    .addParam {
      position: relative;
      height: 30px;

      .addBtn {
        position: absolute;
        right: 0;
        top: -10px;
      }
    }
  }
</style>
