<template >
  <el-dialog
  :class="[$store.state.dialogMarginClass,'import-dialog']"
  :title="importDialogName"
  :width="'450px'"
  :visible.sync="isShow">
    <div class="box" >
      <el-link :underline="false">点击下载：</el-link>
      <el-link
        class="margin-right-10"
        :underline="false"
        @click="exportTemplate"
        type="primary"
      >{{exportFileName}}</el-link>
    </div>
    <div class="box" >
      <span>选择导入的文件：</span>
      <el-input size="small" class="margin-right-10" v-model="importFileName" readonly="readonly"></el-input>
      <el-upload
        action
        class="display-flex file-upload"
        :auto-upload="false"
        accept=".xlsx, .xls"
        ref="upload"
        :http-request="submitFile"
        :file-list="importFiles"
        :show-file-list="false"
        :on-change="selectFile"
        :on-success="handleUploadSuccess"
        :on-error="handleUploadError"
      >
        <el-button size="small" slot="trigger"  >选择</el-button>
      </el-upload>
    </div>
    <div class="box" >
      <p>请避免：</p>
      <p>1. excel 文件格式错误</p>
      <p>2. 参数名称和别名缺少或重复</p>
    </div>
    <span class="display-flex margin-top-15" v-show="isUploadProgressShow">
      <span>{{VARIABLE.UPLOAD_PROGRESS}}：</span>
      <el-progress
        :text-inside="true"
        :percentage="uploadPercent"
        :stroke-width="22"
        :format="formatText"
        :color="uploadProgressColor"
      ></el-progress>
    </span>
    <div class="errorDiv" v-if="errorList.length>0">
      <div class="errorTitle">{{VARIABLE.ERROR_INFO}}:{{errorList.length}}{{VARIABLE.ARTICLE}}</div>
      <div class="errorListDiv" >
        <el-alert
          :title=" item.failData + '  ' + item.failReason "
          v-for="(item,index) in errorList"
          :key="index"
          type="error"
          show-icon
        ></el-alert>
      </div>
    </div>
    <span slot="footer" class="dialog-footer">
        <el-button
          type="primary"
          size="mini" @click="uploadBtnClick"
          :disabled="importFiles.length===0" >{{BTN_TEXT.IMPORT}}</el-button>
        <el-button size="mini" @click="hidePublish()"  >{{BTN_TEXT.CANCEL}}</el-button>
      </span>
  </el-dialog>
</template>

<script>
import { BTN_TEXT, VARIABLE, MSG } from '@/utils/constantsFile';
import { showSuccessMessage, showErrorMessage } from '@/utils/message';
export default {
  components: {},
  props: {
    importDialogName: {
      type: String,
      default: ''
    },
    exportFileName: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      BTN_TEXT: BTN_TEXT,
      VARIABLE: VARIABLE,
      importFileName: '',
      importFiles: [],
      // 上传进度条进度值
      uploadPercent: 0,
      // 进度条是否显示
      isUploadProgressShow: false,
      uploadProgressColor: '#0871dc',
      errorList: [],
      isShow: false
    };
  },
  computed: {},
  created: function() {},
  methods: {
    exportTemplate() {
      this.$emit('exportTemplate');
    },
    /**
     * @description: （“导入用户”对话框）点击“导入”按钮，上传文件
     * @param 无
     * @return: void
     * @author: ZJM
     */
    submitFile(param) {
      this.isUploadProgressShow = false;
      this.uploadPercent = 0;
      this.errorList = [];
      this.$emit('submitImportFile', param);
    },
    /**
     * @description: （“导入用户”对话框）点击“选择文件”按钮，选择需要上传的文件
     * @param {object} file 选中文件的信息
     * @return: void
     * @author: ZJM
     */
    selectFile(file, fileList) {
      // 将选中文件的名称写入input
      this.importFileName = file.name;
      this.importFiles = fileList;
    },
    /**
     * @description: （导入用户对话框）文件上传成功的回调函数
     * @param 无
     * @return: void
     * @author: ZJM
     */
    handleUploadSuccess(res) {
      this.isShow = false;
      showSuccessMessage(MSG.IMPORT_SUCCESS);
      // 刷新表格
      this.$emit('closeImportDialog');
    },
    /**
     * @description: （导入用户对话框）文件上传失败的回调函数
     * @param 无
     * @return: void
     * @author: ZJM
     */
    // eslint-disable-next-line handle-callback-err
    handleUploadError(err) {
      this.isUploadProgressShow = false;
      this.uploadPercent = 0;
      this.$nextTick(() => {
        this.importFileName = '';
      });
      this.importFiles = [];
      // this.resetUploadProgressBar();
      // // this.resetUploadProgressBar();
      // // this.isImportDialogShow = false;
      showErrorMessage(MSG.IMPORT_ERROR);
      this.errorList = err.rows;
    },
    /**
     * @description: 点击“导入”按钮，提交文件
     * @param 无
     * @return: void
     * @author: ZJM
     */
    uploadBtnClick() {
      this.$refs.upload.submit();
      this.uploadPercent = 0;
      // this.isUploadProgressShow = true;
    },
    /**
     * @description: 格式化进度条显示的文字
     * @param {number} percentage 进度百分比
     * @return: void
     * @author: ZJM
     */
    formatText(percentage) {
      return `${percentage}%`;
    },
    /**
     * @description: 重置进度条
     * @param 无
     * @return: void
     * @author: ZJM
     */
    resetUploadProgressBar() {
      this.isUploadProgressShow = false;
      this.uploadPercent = 0;
      // this.$refs.upload.clearFiles();
      this.$nextTick(() => {
        this.importFileName = '';
      });
      // 清空文件列表
      this.importFileName = '';
      this.errorList = [];
    }
  }
};
</script>

<style lang="less"  scoped>
.width-120 {
  min-width: 120px;
}

.margin-right-10 {
  margin-right: 10px;
}

.margin-top-15 {
  margin-top: 15px;
  display: block;
}

.import-dialog {
  /deep/ .el-link--inner {
    line-height: 32px;
  }

  /deep/ .el-input--small {
    max-width: 150px;
  }

  /deep/ .el-dialog__header {
    padding: 10px 10px !important;
  }

  // 进度条样式
  /deep/ .el-progress-bar__outer {
    width: 100%;
  }

  /deep/ .file-upload {
    width: 100px;
    display: inline-block;
  }
}
.errorTitle {
  padding: 0;
  margin: 0;
  height: 40px;
  line-height: 40px;
  font-weight: bold;
  font-size: 20px;
  margin-top: 10px;
}
.el-alert {
  min-height: 25px;
  margin-bottom: 5px;
}
.el-alert /deep/ .el-alert__content{
    min-height: 35px;
    /* line-height: 29px; */
    display: contents;
    /* padding-left: 10px; */
    margin-left: 10px;
}
.el-alert /deep/ .el-alert__title {
    font-size: 13px;
    line-height: 20px;
    text-align: left;
}
.el-alert /deep/ .el-alert__icon {
    font-size: 16px;
    margin-right: 10px;
    width: 16px;
}
.errorListDiv{
  padding: 5px;
  max-height: 300px;
  overflow: auto;
}
.box{
  text-align: left;
  min-height: 30px;
  padding-left: 30px;
  p{
    margin: 0;
    height: 20px;
  }
}
</style>
