<template>
 <div v-if="islist" class="network NetWorkList">
  <div class="header_con">
    <div class="item_oper" @click="handleReload">
      <i class="icon-refresh"></i>
      <span class="name">刷新</span>
    </div>
    <div class="item_oper" @click="insertEvent">
      <i class="icon-add"></i>
      <span class="name">新增接入</span>
    </div>
  </div>
  <div class="mian_con">
    <!-- 头部查询栏 -->
    <vxe-form :data="formData" @submit="listData">
      <vxe-form-item title="网关编码:" :item-render="{}" field="gatewayType">
        <template v-slot>
          <vxe-input v-model="formData.gatewayType" placeholder="请输入"></vxe-input>
        </template>
      </vxe-form-item>
      <vxe-form-item  title="公司" field="companyName" :item-render="{}">
        <template v-slot>
          <vxe-select v-model="formData.companyName" placeholder="请选择" >
            <vxe-option
              v-for="(item,index) in companyList"
              :key="index"
              :value="item.companyName"
              :label="item.companyName"
            ></vxe-option>
          </vxe-select>
        </template>
      </vxe-form-item>
      <vxe-form-item>
        <template v-slot>
          <vxe-button type="submit" status="primary">查询</vxe-button>
          <vxe-button type="reset">重置</vxe-button>
        </template>
      </vxe-form-item>
    </vxe-form>
    <div class="list_con">
      <!-- 表格区域 -->
      <vxe-table
        border
        resizable
        highlight-current-row
        show-overflow
        ref="xTable"
        :tablePage='tablePage'
        :loading="tableLoading"
        :height="$options.filters.getTableHeight(260)"
        :data="tableData"
        >
          <vxe-table-column type="seq" title="序号" width="60"></vxe-table-column>
          <vxe-table-column field="companyName" width="300" title="公司名称" :edit-render="{name: '$select', options: companyList}" ></vxe-table-column>
          <vxe-table-column field="gatewayName" title="网关名称"  ></vxe-table-column>
          <vxe-table-column field="gatewayType" title="网关系列"></vxe-table-column>
          <vxe-table-column field="gatewayCode" title="网关编码" ></vxe-table-column>
          <vxe-table-column field="northLinkState" title="远程连接状态">
            <template v-slot="{row}" >
              <vxe-button :status="row.northLinkState | statusStyle"  :content="row.northLinkState | statusContent" ></vxe-button>
            </template>
          </vxe-table-column>
          <vxe-table-column field="southLinkState" title="设备连接状态">
            <template v-slot="{row}" >
              <vxe-button :status="row.southLinkState | statusStyle"  :content="row.southLinkState | statusContent" ></vxe-button>
            </template>
          </vxe-table-column>
          <vxe-table-column field="bootTime" title="启动时间">
            <template v-slot="{row}" >
              <span>{{row.bootTime | timeSwitch }}</span>
            </template>
          </vxe-table-column>
          <vxe-table-column title="操作" align="center" width="300" show-overflow>
          <template v-slot="{ row }">
            <vxe-button type="text"  status="primary" @click="handelVisit(row)" content="访问"></vxe-button>
            <vxe-button type="text"  status="danger"  content="重启" ></vxe-button>
            <vxe-button type="text" status="primary"  @click="modifyPassword(row,column)" content="修改密码" ></vxe-button>
            <vxe-button type="text" status="danger"  content="重置密码" ></vxe-button>
          </template>
          </vxe-table-column>
      </vxe-table>
       <!-- 页数 -->
       <div class="page_con">
         <vxe-pager
          :loading="tableLoading"
          :border="true"
          background
          :current-page="tablePage.currentPage"
          :page-size="tablePage.pageSize"
          :total="tablePage.total"
          :layouts="tablePage.layouts"
          @page-change="handlePageChange">
        </vxe-pager>
       </div>
    </div>
    <!-- 新增模态框 -->
    <vxe-modal
      ref="xModal"
      v-model="showEdit"
      title='新增接入'
      width="600"
      @close="closeit"
      destroy-on-close
    >
      <template v-slot>
        <vxe-form :data="formTable" ref="roleForm" title-align="right" title-width="100"  @submit="submitEvent" >
          <vxe-form-item title="公司" field="companyCode" span="24" :item-render="{name: '$select', options: NameList}"></vxe-form-item>
          <vxe-form-item title="网关名称" field="gatewayCode" span="24" :item-render="{name: 'input'}"></vxe-form-item>
          <vxe-form-item title="网关系列" field="gatewayName" span="24" :item-render="{name: 'input'}"></vxe-form-item>
          <vxe-form-item  title="网关验证码" field="gatewayType" span="24" :item-render="{name: 'input'}"></vxe-form-item>
          <vxe-form-item :data="formTable" align="center" span="24">
            <template v-slot>
              <vxe-button type="submit" status="primary">确定</vxe-button>
              <vxe-button @click="$refs.xModal.close()">取消</vxe-button>
            </template>
          </vxe-form-item>
        </vxe-form>
      </template>
    </vxe-modal>
    <!-- 修改密码模态框 -->
    <el-dialog title="修改密码"
      :visible.sync="dialogVisible"
      width='500px'
      >
      <el-form :model="form2" ref="roleForm"  @submit.native.prevent >
          <el-row>
            <el-col :span="20" :offset="1">
              <el-form-item label="新密码：" :label-width="formLabelWidth" prop="">
                <el-input
                  type="password"
                  v-model="form2.NewPassWord"
                  autocomplete="off"
                  size="small"
                >
                </el-input>
              </el-form-item>
            </el-col>
            <el-col :span="20" :offset="1">
              <el-form-item label="重复密码" :label-width="formLabelWidth" prop="">
                <el-input
                  type="password"
                  v-model="form2.RePassWord"
                  autocomplete="off"
                  size="small"
                >
                </el-input>
              </el-form-item>
            </el-col>
          </el-row>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button class="custom-button" type="primary" @click="PasswordConfirm(row)">确认</el-button>
          <el-button class="custom-button" @click="dialogVisible = false">取消</el-button>
        </span>
    </el-dialog>
  </div>
 </div>
 <netWork-detail v-else @handleBack="handleBack" :param="detailParam"></netWork-detail>
</template>

<script>
import js from '@/assets/js/main/NetWorkList/index';
export default js;
</script>

<style lang="less" scoped>
.NetWorkList {
  height: calc(100vh - 90px);
  line-height: 100%;
  text-align: left;
  .header_con {
    display: flex;
    width: 100%;
    height: 40px;
    background-color: #eff5ff;

    .item_oper {
      display: flex;
      align-items: center;
      cursor: pointer;
    }
  }
  .mian_con {
    padding: 0 10px;
  }
  .page_con {
    background-color: #f8f8f9;
    border: 1px solid #e8eaec;
    border-top: none;
  }
}
</style>
