<template>
  <div class="ConcatConfigMessage network">
    <div class="tabel_con">
      <vxe-grid
        border="full"
        resizable
        keep-source
        ref="xGrid"
        class="vxe-table-element"
        :max-height="$options.filters.getTableHeight(200)"
        highlight-current-row
        :loading="tableLoading"
        :columns="tableColumns"
        :data="tableDatas"
      ></vxe-grid>
    </div>
    <div class="form_con"  v-if="formVisible">
      <form-module
        :formData="detailFormData"
        :formOptions="detailFormOptions"
        @selectChange="val => selectChange(val, 'detailFormOptions')"
      >
        <template v-slot:formItemSlot>
          <div style="height: 30px"></div>
        </template>
        <template v-slot:footer>
          <el-button size="mini" type="primary" class="m-r20" @click="handleSave">保存修改</el-button>
          <el-button size="mini" @click="handleCancel">取消</el-button>
        </template>
      </form-module>
    </div>
    <el-dialog
      :class="[$store.state.dialogMarginClass]"
      :title="'新增连接'"
      :width="'450px'"
      :visible.sync="dialogVisible"
    >
      <div class="dialog_con">
        <!-- <form-module
          :formData="dialogFormData"
          :formOptions="dialogFormOptions"
          @selectChange="val => selectChange(val, 'dialogFormOptions')"
        >
          <template v-slot:formItemSlot>
            <div style="height: 30px"></div>
          </template>
          <template v-slot:footer>
            <el-button size="mini" type="primary" class="m-r20" @click="handleSaveAdd">确认</el-button>
            <el-button size="mini" @click="dialogVisible = false">取消</el-button>
          </template>
        </form-module> -->
      </div>
    </el-dialog>
  </div>
</template>

<script>
// import FormModule from '../FormModule';
import {
  listCombinedAcquireConfigs,
  listAcquireProtocolTemplateInShort,
  updateAcquireConfig,
  buildLink,
  insertAcquireConfig,
  deleteCloudAcquireConfig,
  deleteEdgeAcquireConfig
} from '@/api/publishConfig';
let allProtocol = [];
const commonOptions = [{
  key: 'configName',
  label: '配置名:'
}, {
  type: 'select',
  key: 'templateId',
  label: '协议:',
  options: []
}, {
  type: 'line',
  key: 'name',
  label: ''
}];
export default {
  name: 'ConcatConfigMessage',
  components: {
    // FormModule
  },
  props: {
    param: {
      type: Object,
      default: () => {
        return {
          gatewayId: null,
          gatewayCode: null
        };
      }
    }
  },
  data() {
    return {
      dialogVisible: false,
      formVisible: false,
      tableLoading: false,
      currentRow: {},
      detailFormData: {},
      dialogFormData: {},
      dialogFormOptions: [],
      detailFormOptions: [],
      // 表头
      tableColumns: [
        {
          type: 'seq',
          field: '',
          title: '序号',
          width: '80',
          align: 'center'
        },
        {
          field: 'configName',
          title: '连接名称',
          align: 'center'
        },
        {
          field: 'protocolName',
          title: '协议名称',
          align: 'center'
        },
        {
          field: 'configStatusEnum',
          title: '连接状态',
          // minWidth: '80',
          align: 'center',
          slots: {
            default: ({ row, column }) => {
              if (row.configStatusEnum === 'IN_USE') {
                return [<span class="concat takeEffect" >已连接</span>];
              } else if (row.configStatusEnum === 'NOT_IN_USE') {
                return [<span class="noConcat takeEffect" >未连接</span>];
              }
            }
          }
        },
        {
          field: 'linkTime',
          title: '上次通信时间',
          // minWidth: '100',
          align: 'center',
          formatter: ['formatDate', 'yyyy-MM-dd HH:mm:ss']
        },
        {
          title: '操作',
          // width: '300',
          align: 'center',
          fixed: 'right',
          slots: {
            default: ({ row, column }) => {
              const list = [
                <vxe-button type="text" status="primary" content="详情" onClick={() => this.handleDatial(row, column)}></vxe-button>
              ];
              if (row.configStatusEnum === 'NOT_IN_USE') {
                list.splice(1, 0, <vxe-button type="text" status="primary" content="连接" onClick={() => this.handleConcat(row, column)}></vxe-button>);
                list.splice(2, 0, <vxe-button type="text" status="danger" content="删除" onClick={() => this.handleDel(row, deleteCloudAcquireConfig, '删除')} ></vxe-button>);
              } else {
                list.splice(1, 0, <vxe-button type="text" status="danger" content="远程删除" onClick={() => this.handleDel(row, deleteEdgeAcquireConfig, '远程删除')}></vxe-button>);
              }
              return list;
            }
          }
        }
      ],
      // 表格字段
      tableDatas: []
    };
  },
  mounted() {
    this.loadData();
    this.getProtocol();
  },
  methods: {
    // 刷新
    reLoad() {
      this.loadData();
    },
    // 加载数据
    loadData() {
      this.tableLoading = true;
      listCombinedAcquireConfigs({
        gatewayCode: this.param.gatewayCode,
        gatewayId: this.param.gatewayId
      }).then(res => {
        this.tableDatas = res.data || [];
        this.tableLoading = false;
      });
    },
    // 获取协议列表
    getProtocol() {
      listAcquireProtocolTemplateInShort().then(res => {
        allProtocol = res.data || [];
        commonOptions[1].options = res.data?.map(item => {
          return {
            label: item.protocolName,
            value: item.id
          };
        });
      });
    },
    // 新增连接
    handleAddConcat() {
      this.dialogFormOptions = commonOptions;
      this.dialogVisible = true;
    },
    // 保存新增
    handleSaveAdd() {
      const { templateId, configName } = this.dialogFormData;
      delete this.dialogFormData.templateId;
      delete this.dialogFormData.configName;
      insertAcquireConfig({
        gatewayId: this.param.gatewayId,
        templateId,
        configName,
        linkDefinitionContent: JSON.stringify(this.dialogFormData)
      }).then(res => {
        this.dialogVisible = false;
        this.loadData();
        this.$message({
          message: '新增成功',
          type: 'success'
        });
      });
    },
    // 改变协议
    selectChange(id, name) {
      const paramStr = allProtocol.filter(item => item.id === id)[0]?.protocolDefinition?.map(para => {
        return {
          key: para.name,
          label: para.explaination + ':'
        };
      });
      this[name] = [...commonOptions, ...paramStr];
    },
    // 详情
    handleDatial(row, column) {
      this.formVisible = true;
      this.currentRow = row;
      const { configName, templateId, protocolDefinitionContent } = row;
      this.detailFormData = {
        configName,
        templateId,
        ...protocolDefinitionContent
      };
      const paramStr = allProtocol.filter(item => item.id === templateId)[0]?.protocolDefinition?.map(para => {
        return {
          key: para.name,
          label: para.explaination + ':'
        };
      }) || [];
      this.detailFormOptions = [...commonOptions, ...paramStr];
    },
    // quxiao
    handleCancel() {
      this.handleDatial(this.currentRow);
    },
    // 连接
    handleConcat(row) {
      buildLink({
        acquireConfigId: row.id, // 采集配置ID
        gatewayCode: this.param.gatewayCode // 网关编码
      }).then(res => {
        this.loadData();
        this.$message({
          message: '连接成功',
          type: 'success'
        });
      });
    },
    // 保存修改
    handleSave() {
      const { templateId, configName } = this.detailFormData;
      delete this.detailFormData.templateId;
      delete this.detailFormData.configName;
      updateAcquireConfig({}, {
        id: this.currentRow.id,
        gatewayId: this.currentRow.gatewayId,
        templateId,
        configName,
        protocolDefinitionContent: JSON.stringify(this.detailFormData)
      }).then(res => {
        this.$message({
          message: '保存成功',
          type: 'success'
        });
      });
    },
    // 删除
    handleDel(row, apiName, type) {
      apiName({
        gatewayId: this.param.gatewayId,
        id: row.id,
        gatewayCode: type === '远程删除' ? '001' : undefined
      }).then(res => {
        this.loadData();
        this.$message({
          message: '删除成功',
          type: 'success'
        });
      });
    }
  }
};
</script>

<style lang="less" scoped>
.ConcatConfigMessage {
  display: flex;
  justify-content: space-between;
  height: 100%;
  padding: 10px;

  .tabel_con {
    width: 74%;
    height: 100%;
  }
  .form_con {
    width: 25%;
    height: 100%;
    overflow-y: auto;
    padding: 25px 50px;
    border: 1px solid #DFDFDF;
    box-shadow: 0px 5px 8px 0px rgba(0, 0, 0, 0.14);
    border-radius: 1px;
  }
  .dialog_con {
    padding: 0 30px;
  }
  .takeEffect {
    display: inline-block;
    width: 70px;
    height: 28px;
    line-height: 28px;
    // font-weight: bold;
    color: #000000;
    border-radius: 14px;
  }
  .concat {
    background: #CAFBEB;
  }
  .noConcat {
    background: #D5D5D5;
  }
}
</style>
